////
/// Key information box
/// @group patterns
////

@mixin content-slider($color) {
    align-items: center;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    margin-top: $measure--basic;

    .key-information--lifelong-learning__current {
        @include deltatype();
        margin-right: $measure--small4;
    }

    &[hidden] .key-information--lifelong-learning__current,
    &[hidden] .arrow-left--btn-prev,
    &[hidden] .arrow-right--btn-next {
        display: none;
    }

    .key-information--lifelong-learning__current__digit {
        border-bottom: $border-width-default $border-style-solid $color-pink80;
        color: $color-pink80;
    }

    .key-information--lifelong-learning__current__total {
        border-bottom: $border-width-default $border-style-solid transparent;
    }

    .arrow-left--btn-prev,
    .arrow-right--btn-next {
        @include deltatype();
        background: $color-purewhite;
        border: $border-width-thicker $border-style-solid $color;
        color: $color;
        padding: $measure--half;
        width: $measure--medium3;

        @include media('>=tablet') {
            display: none; // Feature only visible on smaller devices
        }

        &:disabled {
            opacity: 0.3;
        }
    }

    .arrow-left--btn-prev {
        left: -$measure--small2;
        margin-right: $measure--small4;
    }

    .arrow-right--btn-next {
        right: -$measure--small2;
    }
}

.key-info {
    @include standard-box();
    // Container grids
    .content-wrapper {
        @include media('<tablet') {
            grid-template-columns: repeat(4, 1fr);
            grid-template-areas: 'c c c c';
            grid-column-gap: $measure--basic;
        }

        .content {
            grid-area: c;
            background: $color-gray1;
            padding: $measure--basic;
        }
    }

    &.key-info--lifelong-learning-paginated {
        ul {
            max-width: unset;
            margin-left: 0;
            @include media('<tablet') {
                margin: 0;
            }
        }
    }
}

.key-info__data-item--centered {
    text-align: center;
}

.key-info__dates-quantity {
    padding: 0 0 $measure--small1 0;
    color: $color-pink90;
    border-bottom: $border-width-hairline $border-style-solid $color-gray6;
    font-size: $font-size--smalltype;
}

.key-information--lifelong-learning > ul .hide {
    opacity: 0;
    display: none;
}

.key-info__listing {
    display: grid;
    // Opacity value needed for 'Load more' fade in
    opacity: 1;
    transition: opacity 1s linear;

    &.hide {
        opacity: 0;
        display: none;
    }

    /* Content column grids */
    @include media('<tablet') {
        padding: $measure--basic;
        margin-top: 0;
        background-color: $color-gray1;
        grid-template-columns: repeat(4, 1fr);
        grid-template-areas:
            'q q q q'
            'h h h h'
            'c1 c1 c1 c1'
            'c2 c2 c2 c2'
            'c3 c3 c3 c3'
            'm m m m';
    }

    @include media('>=tablet', '<between') {
        grid-column-gap: $measure--small2;
        grid-template-columns: repeat(8, 1fr);
        grid-template-rows: repeat(4, auto);
        grid-template-areas:
            'q q q q q q q q'
            'h h h h h h h h'
            'c1 c1 c1 c2 c2 c2 c3 c3'
            'm m m m m m m m';
    }

    @include media('>between') {
        grid-column-gap: $measure--small2;
        grid-template-columns: repeat(12, 1fr);
        grid-template-rows: repeat(4, auto);
        grid-template-areas:
            'q q q q q q q q q q q q'
            'h h h h h h h h h h h h'
            'c1 c1 c1 c1 c1 c2 c2 c2 c2 c3 c3 c3'
            'm m m m m m m m m m m m';
        border-bottom: $border-width-hairline $border-style-dotted $color-gray4;
        padding: $measure--small3 0;
    }
}

.key-info__dates-quantity {
    grid-area: q;
}

.key-info__date {
    grid-area: h;
    font-size: $font-size--alpha;
    color: $color-gray10;
    font-weight: $font-weight-bold;
}

.key-info__col-one {
    grid-area: c1;
}

.key-info__col-two {
    grid-area: c2;
}

.key-info__col-three {
    grid-area: c3;
}

.key-information--lifelong-learning__controls {
    .content-slider {
        @include content-slider($color-pink90);
    }
}
