.show-more__button__container {
    margin-top: $measure--medium1;
    text-align: center;
}

.show-more__text {
    @include content-style;

    overflow: hidden;
    transition: max-height 0.5s;
}

.show-more__text[data-hidden='true'] {
    max-height: $measure--huge1;
    position: relative;

    &:after {
        background-image: linear-gradient(
            to bottom,
            rgba(255, 255, 255, 0),
            rgba(255, 255, 255, 1) 90%
        );
        bottom: 0;
        content: '';
        height: 90%;
        left: 0;
        position: absolute;
        width: 100%;
        z-index: z-index('above');
    }
}

.show-more__button {
    border-top-color: $color-linkblue--dark;
    border-top-style: $border-style-solid;
    border-top-width: $border-width-default;
    padding-top: $measure--half;

    span.icon {
        margin-right: $measure--quarter;
        border-bottom: $border-width-default $border-style-solid transparent;
    }

    span.show-more__link-text {
        border-bottom: $border-width-default $border-style-solid $color-linkblue;
    }

    .fa-minus-circle {
        display: none;
    }

    .fa-plus-circle {
        display: inline;
    }

    [data-open='true'] {
        .fa-minus-circle {
            display: inline;
        }

        .fa-plus-circle {
            display: none;
        }
    }

    a {
        @include anchor-inline(
            $color-inline-link,
            $color-inline-link,
            $color-text--dark,
            $color-outline
        );

        border-bottom: none;

        &:hover,
        &:active {
            border-bottom: none;
            span.icon {
                border-bottom: $border-width-default $border-style-solid
                    transparent;
            }
            span.show-more__link-text {
                border-bottom: $border-width-default $border-style-solid
                    transparent;
            }
        }

        &:focus {
            border-bottom: none;
            span.icon {
                border-bottom: $border-width-default $border-style-solid
                    transparent;
            }
            span.show-more__link-text {
                border-bottom: $border-width-default $border-style-solid
                    $color-linkblue;
            }
        }
    }
}
