// Grey wrapper (between banner and flexible content section)
.course--pgt__section-wrapper--gray {
    margin-bottom: $measure--small4;
}

// Banner
.course--pgt {
    .banner__overlay {
        background: linear-gradient(to top, $color-pink80, transparent 100%);
        .banner__overlay__heading {
            grid-column: 1/-1;
            @include media('>tablet') {
                grid-column: 3/-3;
            }
            padding: 0;
            margin: 0;
        }
    }

    .wrapper--course--pgt__introduction {
        @include content-grid();
        padding-top: $measure--medium3;
        @include media('<=small') {
            @include standard-box();
        }
        @include media('>tablet') {
            padding-bottom: $measure--basic;
            margin-top: 0;
        }

        a,
        button {
            background-image: none;

            &:focus,
            &:active {
                @include outline-properties($color-purewhite);
            }
        }

        .course--pgt__introduction {
            color: $color-purewhite;
            grid-column: 1/-1;
            @include media('>tablet') {
                grid-column: 3/-3;
            }

            .course--pgt__introduction__year-select {
                grid-column: 3 / 10;

                @include media('<=tablet') {
                    grid-column: 1 / -1;
                    text-align: center;
                }

                p {
                    font-weight: $font-weight-book;
                    font-size: $font-size--beta;

                    @include media('<=tablet') {
                        max-width: unset;
                    }

                    a {
                        color: $color-gray4;
                        &:focus,
                        &:active {
                            outline-color: $color-gray4;
                        }
                        font-weight: $font-weight-normal;
                        border-bottom: 0;
                        text-decoration: underline;
                    }
                }
            }

            .course--pgt__introduction__ctas {
                grid-column: 1 / -1;
                padding: $measure--basic 0;
                text-align: center;

                @include media('>tablet') {
                    grid-column: 3/-2;
                    display: flex;
                    justify-content: space-between;
                }

                @include media('<=tablet') {
                    > .course--pgt__introduction__ctas--apply,
                    > .course--pgt__introduction__ctas--video {
                        > .cta-block {
                            max-width: unset;

                            > .cta-block__cta {
                                margin-left: auto;
                                margin-right: auto;
                                max-width: unset;
                            }
                        }
                    }
                    > .how-to-apply--pgt--js {
                        .cta-block__cta {
                            width: 100%;
                            .primary-cta-arrow--bright {
                                width: 100%;
                                text-align: center;
                                span {
                                    margin: 0 auto;
                                }
                            }
                        }
                    }
                }

                .course--pgt__introduction__ctas--video {
                    @include media('<=tablet') {
                        margin-top: $measure--small2;

                        .cta-block__cta {
                            // margin: $measure--small2 auto;
                        }
                    }

                    .dialog__inner {
                        text-align: left;
                        color: $color-gray10;
                        width: 90%;
                        @include media('>tablet') {
                            width: 60%;
                        }
                        .dialog__close {
                            outline-offset: 0;
                            &:focus {
                                outline-color: $color-outline;
                            }
                        }
                    }

                    span {
                        display: block;
                        @include media('>tablet') {
                            display: inline-block;
                        }
                    }

                    .cta-block {
                        @include media('>tablet') {
                            margin-left: $measure--basic;
                            display: inline-block;
                        }

                        .cta-block__cta {
                            margin-right: 0;
                            @include media('<=tablet') {
                                margin: $measure--small2 auto;
                            }
                            .outline-cta-arrow--dark {
                                &:after {
                                    content: '\f144';
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}

// Promo content
.course--pgt__promo__content {
    @include content-grid();
    @include media('<small') {
        @include standard-box();
    }
    .promo-box {
        grid-column: 1/-1;
        margin-top: $measure--small4;
        @include media('>tablet') {
            grid-column: 3/-3;
        }
    }
}

.wrapper--course--pgt__profiles__heading,
.wrapper--course--pgt__facilities__heading {
    @include content-grid();
    @include full-width-heading();
    grid-column: 1/-1;
    @include media('<small') {
        @include standard-box();
    }
}

// Profiles
.course--pgt__profiles__content {
    grid-column: 1/-1;
    @include media('<small') {
        @include standard-box();
    }
    .wrapper--featured-profiles__heading-intro,
    .wrapper--featured-profiles__profile {
        @include media('<=tablet') {
            grid-column: 1/-1;
        }
    }
}

// Key information
.course--pgt__keyinfo {
    padding-top: $measure--small4;
    @include content-grid();
    @include media('<small') {
        @include standard-box();
    }
    .course--pgt__keyinfo__content {
        // General
        grid-column: 1/-1;
        @include media('>tablet') {
            grid-column: 3/-3;
        }
        .course--pgt__keyinfo__content__column__item {
            margin-bottom: $measure--basic;
            :not(span) {
                font-family: $font-family-meta;
                font-weight: $font-weight-medium;
                @include betatype();
            }
        }
        span {
            display: block;
            margin-bottom: $measure--quarter;
            color: $color-gray9;
            font-weight: $font-weight-book;
            @include font-family('default');
            &.label {
                @include font-family('heading');
                color: $color-gray7;
                font-weight: $font-weight-normal;
                &.color--pink80 {
                    color: $color-pink80;
                    font-weight: $font-weight-medium;
                }
            }
        }

        // By section
        .course--pgt__keyinfo__content__standfirst {
            padding: $measure--small3;
            background: $color-purewhite;
            margin: 0 0 $measure--small4;
            .course--pgt__keyinfo__content__heading {
                margin-top: 0;
            }
        }

        .wrapper--course--pgt__keyinfo__content__date-location {
            background: $color-purewhite;
            padding: $measure--basic $measure--small3 $measure--basic;
            .course--pgt__keyinfo__content__date-location {
                background: $color-purewhite;
                padding-top: $measure--threequarters;
                padding-bottom: $measure--basic;
                @include media('>tablet') {
                    border-bottom: $border-width-default $border-style-solid
                        $color-gray3;
                }
            }
        }

        .wrapper--course--pgt__keyinfo__content__duration {
            background: $color-purewhite;
            padding: 0 $measure--small3 $measure--basic;
            .course--pgt__keyinfo__content__duration {
                display: grid;
                grid-column-gap: $measure--small2;
                grid-template-columns: repeat(14, 1fr);
                @include media('>tablet') {
                    border-bottom: $border-width-default $border-style-solid
                        $color-gray3;
                }
                .course--pgt__keyinfo__content__duration__qualification-type {
                    margin-bottom: $measure--basic;
                    grid-column: 1/-1;
                    @include media('>tablet') {
                        // Larger screens: group first qualification type in first column; others in second
                        &:nth-child(1) {
                            grid-column: 1/7;
                        }
                        &:nth-child(n + 2) {
                            grid-column: 7/-1;
                        }
                    }
                }
            }
        }

        .course--pgt__keyinfo__content__accreditation {
            background: $color-purewhite;
            padding-bottom: $measure--small1;
            @include media('>tablet') {
                display: flex;
                padding-top: $measure--small1;
                border-bottom: $border-width-default $border-style-solid
                    $color-gray3;
            }
            .course--pgt__keyinfo__content__column {
                &:nth-child(1) {
                    flex-basis: 33%;
                }
                &:nth-child(2) {
                    flex-basis: 66%;
                }
            }
        }

        // CTAs
        ul.course--pgt__keyinfo__content__ctas {
            grid-column: 1 / -1;
            margin: $measure--basic 0;
            max-width: none;

            @include media('>tablet') {
                grid-column: 3 / -3;
                display: flex;
            }

            li {
                list-style-type: none;
                a {
                    width: 100%;
                    height: 100%;
                    box-shadow: none;
                    &:hover {
                        span {
                            color: $color-purewhite;
                        }
                    }
                    span {
                        margin: auto; // Centre CTA text
                    }
                }
            }
        }
    }
}

// Our students
.course--pgt__our-students {
    .wrapper--course--pgt__our-students__heading {
        @include content-grid();
        @include full-width-heading();
        grid-column: 1/-1;
        @include media('<small') {
            @include standard-box();
        }
    }

    .course--pgt__student-work {
        @include content-grid();
        @include media('<small') {
            @include standard-box();
        }
        .course--pgt__student-work__heading,
        .wrapper--course--pgt__student-work__video {
            grid-column: 1/-1;
            @include media('>tablet') {
                grid-column: 2/-1;
            }
        }

        // Video
        .wrapper--course--pgt__student-work__video {
            @include media('>tablet') {
                display: flex;
                .embedded-video {
                    padding-left: 0;
                    flex: 1;
                    &:last-of-type {
                        padding-right: 0;
                    }
                }
            }

            .embedded-video {
                padding-top: 0;
                @include media('<=tablet') {
                    padding-left: 0;
                    padding-right: 0;
                    margin-top: $measure--small4;
                    margin-bottom: $measure--small4;
                }
            }
        }

        // Image grid
        .imageColLayout {
            grid-column: 1/-1;
            @include media('>tablet') {
                grid-column: 2/-1;
            }
        }
    }
}

// USP
.course--pgt__usp {
    @include content-grid();
    @include media('<small') {
        @include standard-box();
    }
    .course--pgt__usp__content {
        grid-column: 1/-1;
        @include media('>tablet') {
            grid-column: 3/-3;
        }
        .course--pgt__usp__heading {
            font-weight: $font-weight-light;
            margin-bottom: $measure--basic;
        }
        .statistics-block {
            padding: $measure--basic 0;
            background: $color-purewhite;
            .ksp__items {
                margin: 0 $measure--threequarters;
                &.ksp__items--2 {
                    grid-template-areas:
                        'a a a a a a'
                        'b b b b b b';
                    @include media('>tablet') {
                        grid-template-areas: 'a a a a a a b b b b b b';
                    }
                    @include media('>small') {
                        grid-template-areas: 'a a a a a a a a a b b b b b b b b b';
                    }
                }
                .ksp__item__icon {
                    margin: 0 auto;
                }
            }
        }
    }
}

// Our students
.course--pgt__our-students {
    .wrapper--course--pgt__our-students__heading {
        @include content-grid();
        @include full-width-heading();
        grid-column: 1/-1;
    }

    .course--pgt__student-work {
        grid-column: 1/-1;
        @include content-grid();
        .course--pgt__student-work__heading,
        .wrapper--course--pgt__student-work__video {
            grid-column: 1/-1;
            @include media('>tablet') {
                grid-column: 2/-1;
            }
        }
        .wrapper--course--pgt__student-work__video {
            @include media('>tablet') {
                display: flex;
                .embedded-video {
                    padding-left: 0;
                    flex: 1;
                    &:last-of-type {
                        padding-right: 0;
                    }
                }
            }
            .embedded-video {
                padding-top: 0;
                @include media('<=tablet') {
                    padding-left: 0;
                    padding-right: 0;
                    margin-top: $measure--small4;
                    margin-bottom: $measure--small4;
                }
            }
        }
    }
}

// Student life
.wrapper--course--pgt__student-life {
    margin: $measure--basic 0 0 0;
    padding: $measure--basic 0 $measure--small4;
    .course--pgt__student-life {
        @include content-grid();
        @include media('<small') {
            @include standard-box();
        }
        .pathway-image-cards {
            grid-column: 1/-1;
            @include media('>tablet') {
                grid-column: 2/-2;
                padding: 0;
            }
        }
    }
}

// What's happening cards
.wrapper--course--pgt__whats-happening {
    padding: $measure--basic 0;
    @include media('>tablet') {
        padding: $measure--basic 0 $measure--medium3;
    }
    @include content-grid();
    @include media('<small') {
        @include standard-box();
    }
    .course--pgt__whats-happening {
        @include sixteen-grid();
        grid-column: 1/-1;
        @include media('>tablet') {
            grid-column: 2/-2;
        }
        .related-content__heading {
            grid-column: 1/-1;
            @include media('>tablet') {
                grid-column: 1/7;
            }
        }
        .related-content {
            grid-column: 1/-1;
            @include media('>=tablet') {
                display: flex;
                flex-wrap: wrap;
                .card {
                    flex: 1;
                    height: fit-content;
                    margin-top: $measure--basic;
                    @include media('>tablet') {
                        &:not(:last-of-type) {
                            margin-right: $measure--basic;
                        }
                    }
                }
            }
        }
    }
}

// Student testimonials and Unibuddy
.wrapper--course--pgt__testimonials-unibuddy {
    @include content-grid();
    @include media('<small') {
        @include standard-box();
    }
    margin-bottom: $measure--medium3;

    .course--pgt__testimonials {
        grid-column: 1/-1;
        @include media('>tablet') {
            grid-column: 8/-2;
        }
        .slider {
            grid-column: 1/-1;
            display: block;
            margin-bottom: $measure--basic;

            li {
                margin-top: 0;
                picture {
                    margin-left: 0;
                }
            }
        }
    }
    .course--pgt__unibuddy {
        grid-column: 1/-1;
        margin-bottom: $measure--basic;
        @include media('>tablet') {
            grid-column: 2/8;
        }
        height: fit-content;
        .unibuddy-card {
            margin-top: $measure--small2;
        }
    }
    .course--pgt__testimonials {
        .card--testimonial__photo {
            margin-top: $measure--threequarters;
            margin-left: 0;
        }
        .card--testimonial__quote {
            margin-top: $measure--basic;
        }
    }
}

// Contact cards
.course--pgt__contact-cards .footer-cards {
    color: $color-purewhite;
    h2 {
        border-bottom: $border-width-hairline $border-style-solid $color-pink70;
    }

    a {
        @include anchor-inline(
            $color-gray4,
            $color-gray4,
            $color-gray11,
            $color-outline--dark
        );
    }

    grid-column: 1/-1;
    @include media('>tablet') {
        @include content-grid();
        grid-column: 3/-3;
    }
    padding-left: 0;
    padding-right: 0;
    @include media('<=small') {
        @include default-padding();
    }
    @include media('>tablet') {
        h2,
        ul {
            grid-column: 3/-3;
        }
    }
}

// Related courses
.course--pgt__related-courses {
    @include media('<=small') {
        @include default-margin();
    }
    padding: $measure--small4 0 $measure--medium8;

    h2.related-content__heading {
        @include heading--centered($color-pink90);
    }

    .related-content {
        @include media('>=tablet') {
            display: flex;
            flex-wrap: wrap;
            .card--related-course {
                flex: 1;
            }
        }
    }
}

.course--pgt__tabs {
    margin: $measure--small4 $measure--basic $measure--small4;
    @include media('<small') {
        @include standard-box();
    }

    // Pre-chart heading made up of number and text
    .chart-heading {
        display: flex;
        color: $color-pink90;
        .chart-heading__number {
            flex: 1 1 auto;
            margin: $measure--threequarters $measure--half 0 0;
            font-weight: $font-weight-light;
            @include etatype();
        }
        .chart-heading__text {
            flex: 1 1 auto;
            @include standfirst();
        }
    }
}

.course--pgt {
    .tabs__panel__heading {
        text-align: center;
    }

    .wrapper--tabs__panel__body__content {
        @include media('>tablet') {
            margin: $measure--basic $measure--huge2 0;
        }
    }
    .wrapper--tabs__panel__body__content--wide {
        @include media('>tablet') {
            margin: $measure--basic $measure--medium7 0;
        }
        padding: $measure--basic;
        .card--testimonial .card--testimonial__text .card--testimonial__quote,
        .card--testimonial .card--testimonial__photo {
            margin-top: 0;
        }
    }

    .wrapper--tabs__panel__body__fees__qualification-type__method-of-study {
        @include media('>mobile') {
            display: flex;
            margin-bottom: $measure--medium1;
        }

        .tabs__panel__body__fees__qualification-type__method-of-study {
            @include media('>mobile') {
                flex: 1 0 33.33%;
                font-weight: $font-weight-book;
                color: $color-gray7;
            }
        }

        p.tabs__panel__body__domicile__fees {
            color: $color-pink80;
            @include h5style;
            font-weight: $font-weight-book;
        }

        .tabs__panel__body__domicile {
            margin-right: $measure--small1;

            h5 {
                color: $color-gray7;
            }

            &:last-child {
                margin-right: 0;
            }
        }

        .tabs__panel__body__qualification-type {
            margin: $measure--small5 0;
        }
    }

    .chart-row {
        display: block; // Required for chart columns flex display
        margin: $measure--small4 0 0 0;
        padding: 0;
        .chart {
            margin-right: 0;
            .chart__columns {
                display: flex;
                .chart--piechart {
                    flex-basis: 40%;
                    margin-top: 0;
                }
                .chart__legend {
                    flex-basis: 60%;
                }
            }
        }
    }
}

// Accordion-tabs styles when JS is enabled
.js .course--pgt .accordion {
    padding-bottom: $measure--small4;

    & > .accordion__heading + .accordion__body {
        background-color: $color-purewhite;
    }

    &.accordion--nested .accordion__heading + .accordion__body {
        background-color: $color-gray1;
    }

    & > .accordion__heading {
        background-color: $color-purewhite;
        border-bottom-color: $color-gray3;
    }
}

.js .course--pgt .tabs {
    padding-bottom: $measure--small4;

    .tabs__links {
        background-color: $color-purewhite;
    }
    .tabs__panel {
        background-color: $color-purewhite;
        border-top: none;
    }
}

.chart--bar--horizontal__collection[data-label='course--pgt__msc'] {
    width: 100%;
}

.chart--bar--horizontal__collection[data-label='course--pgt__pgdip'] {
    width: 66%;
}

.chart--bar--horizontal__collection[data-label='course--pgt__pgcert'] {
    width: 33%;
}

.course--pgt .pathway-image-cards {
    @include pathway-image-cards(
        $color-pink80,
        $color-purewhite,
        $color-purewhite,
        $color-pink60,
        $color-gray4,
        $color-gray4,
        $color-purewhite,
        $color-purewhite
    );
    h2 {
        text-align: left;
    }
    .pathway-image-cards__blocks {
        margin-top: $measure--basic;
    }
}

.course--pgt__facilities {
    .wrapper--course--pgt__facilities__video {
        @include content-grid();
        .course--pgt__facilities__video__introduction {
            grid-column: 1/-1;
            @include media('>tablet') {
                grid-column: 2/8;
            }
        }
        .course--pgt__facilities__video__embed {
            grid-column: 1/-1;
            @include media('>tablet') {
                grid-column: 9/-2;
            }
            .embedded-video {
                padding-left: 0;
                padding-right: 0;
            }
        }
    }
    .wrapper--course--pgt__facilities__gallery {
        @include content-grid();
        @include media('<small') {
            @include standard-box();
        }
        .wrapper--course--pgt__facilities__heading {
            margin: 0;
        }
        .course--pgt__facilities__gallery__introduction {
            grid-column: 1/-1;
            @include media('>tablet') {
                grid-column: 2/-1;
            }
        }
        .swiper-viewport {
            @include content-grid();
            grid-column: 1/-1;
        }
    }
}

// Awards: Duration, credits, modules asset listings
.course--pgt__tabs #structure-content {
    .wrapper--course--pgt__awards__content__duration__qualification-type {
        border: $border-width-hairline $border-style-solid $color-gray3;
        background-color: $color-gray1;
        padding: $measure--half $measure--basic;
        margin: $measure--threequarters 0;
    }

    .course--pgt__awards__content__duration__qualification-type,
    .wrapper--course--pgt__awards__content__duration__method-of-study,
    .course--pgt__awards__content__credits {
        border-bottom: $border-width-hairline $border-style-solid $color-gray3;
        padding-bottom: $measure--threequarters;
    }

    .course--pgt__awards__content__duration__method-of-study,
    .wrapper--course--pgt__awards__content__modules ul {
        margin: $measure--quarter 0 $measure--basic $measure--medium2;
    }

    .course--pgt__awards__content__credits p {
        @include media('<=tablet') {
            &:last-of-type {
                margin-bottom: 0;
            }
        }
    }
}

/** No JS **/
.no-js .course--pgt {
    // CTA video modal
    .wrapper--course--pgt__introduction {
        .course--pgt__introduction {
            @include media('>tablet') {
                grid-column: 3/10;
            }
            .course--pgt__introduction__ctas {
                display: block; // Appear underneath 'Apply now' CTA
                .course--pgt__introduction__ctas--video {
                    span {
                        display: none;
                    }
                    .cta-block {
                        margin: $measure--small4 0 0 0;
                        li {
                            figcaption {
                                text-align: left;
                                color: $color-purewhite;
                                span {
                                    display: block;
                                }
                                time {
                                    display: flex;
                                    .icon {
                                        margin: $measure--quarter $measure--half
                                            0 0;
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}
