@mixin screenreader-only {
    border: 0;
    clip: rect(0, 0, 0, 0);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 1px;
}

@mixin sr-only-focusable-within {
    &:active,
    &:focus,
    &:focus-within {
        clip: auto;
        height: auto;
        margin: 0;
        overflow: visible;
        position: static;
        width: auto;
    }
}

.sr-only-focusable-within {
    @include sr-only-focusable-within();
}

@mixin box-shadow {
    box-shadow: 0 $measure--quarter $measure--basic 0 rgba(0, 0, 0, 0.15);
}

@mixin veil() {
    background-color: rgba(30, 30, 30, 0.3);
    bottom: 0;
    display: none;
    left: 0;
    position: absolute;
    right: 0;
    top: 0;
    z-index: z-index('veil');

    &[data-on='true'] {
        display: block;
    }
}

@mixin background-color-and-scale(
    $background-color: $color-gray1,
    $scale: 1.03,
    $duration: $transition-duration--long
) {
    &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        transition: transform $duration;
    }

    &:hover {
        @include media('hover') {
            &:after {
                background-color: $background-color;
                transform: scale($scale);
                z-index: z-index('below');
            }
        }
    }

    .card__inner {
        z-index: z-index('default');
        position: relative;
    }
}

@mixin thumbnail-gradient-scale($scale: 1.04, $background-opacity: 0.1) {
    &:hover {
        @include media('hover') {
            background-image: none;

            img {
                transform: scale($scale);
            }

            .thumbnail__image-container {
                &:after {
                    background-color: rgba(0, 0, 0, $background-opacity);
                }
            }
        }
    }

    .thumbnail__image-container {
        overflow: hidden;
        position: relative;
        transition: transform $transition-duration ease-in;
        width: $measure--huge3;
        height: $measure--huge1;

        @include media('reducemotion') {
            transition: none;
        }

        &:after {
            background-color: transparent;
            content: '';
            display: block;
            height: 100%;
            left: 0;
            position: absolute;
            top: 0;
            transition: background-color $transition-duration ease-in;
            width: 100%;
        }

        img {
            transition: transform $transition-duration ease-in;
            object-fit: cover;
            height: 100%;
            width: 100%;

            @include media('reducemotion') {
                transition: none;
            }
        }
    }
}

@mixin card-shared-properties(
    $title-color: $color-gray7,
    $paragraph-color: $color-gray10
) {
    position: relative;
    transition: none;
    padding: $measure--basic;

    &:hover {
        @include media('hover') {
            background-image: none;
        }
    }

    &:focus,
    &:active {
        &:after {
            @include outline-properties();
        }
    }

    &:visited {
        .title {
            color: $color-inline-link--visited;
        }
    }

    .card__title {
        @include h3style();
        @include betatype();

        color: $title-color;
    }

    p {
        @include alphatype();

        color: $paragraph-color;
    }
}

/**
    Used where card has two divs, one most likely
    being a thumbnail.
**/
@mixin card-with-thumbnail {
    display: block;

    @include media('>=tablet') {
        display: flex;
    }

    > :nth-child(2) {
        margin-top: $measure--basic;

        @include media('>=tablet') {
            margin-top: 0;
        }
    }
}

/*
    USAGE:

    $pseudo: both / before / after
    $background-color: any hex color
    $skew: a degree value

    Background color must match background color of element mixin is applied to
*/

@mixin angled-edge(
    $pseudo: both,
    $background-color: $color-gray5,
    $skew: 4deg
) {
    position: relative;

    &:before,
    &:after {
        position: absolute;
        left: 0;
        right: 0;
        z-index: -1;
        content: '';
        display: none;
        background-color: $background-color;
        height: $measure--medium11;
        transform: skewY($skew);
    }

    &:before {
        top: -$measure--medium3;

        @if ($pseudo == before or $pseudo == both) {
            display: block;
        }
    }

    &:after {
        bottom: -$measure--medium3;

        @if ($pseudo == after or $pseudo == both) {
            display: block;
        }
    }
}

@mixin gradient-border($color1: $color-red50, $color2: $color-pink60) {
    border: $measure--threequarters solid;
    border-image-source: linear-gradient(45deg, $color1, $color2);
    border-image-slice: 1;
}

////
// this button mixin is used on: testimonial slider, image-gallery
////
@mixin control-button($color1: $color-red50) {
    color: $color1;
    border: $border-width-hairline solid $color1;
    padding: $measure--threequarters;
    font-weight: $font-weight-normal;

    &:hover {
        @include media('hover') {
            color: $color-purewhite;
            background: $color1;
        }
    }

    &:focus,
    &:active {
        color: $color-purewhite;
        background: $color1;
        @include anchor-default-outline($color-outline);
    }

    &:disabled {
        color: $color-gray7;
        background: $color-purewhite;
        border: none;
        opacity: 0.7;
    }
}

@mixin heading-centered() {
    text-align: center;

    &:after {
        content: '';
        width: $measure--medium1;
        height: $measure--quarter;
        display: block;
        background: $color-red50;
        margin: $measure--quarter auto $measure--basic auto;
    }
}

.heading-centered {
    @include heading-centered();
}

@mixin svg-path-animation {
    transition: stroke-dashoffset 1.5s;

    &.animate--svg-path--jsinit {
        display: none;
    }

    &.animate--svg-path--jsrun {
        display: block;
    }
}

@mixin corner-gradient-background(
    $main-color: $color-red60,
    $corner-color: $color-red70
) {
    background-color: $main-color;

    background: linear-gradient(55deg, $main-color 70%, $corner-color 0%);

    @include media('>tiny') {
        background: linear-gradient(55deg, $main-color 75%, $corner-color 0%);
    }

    @include media('>=mobile') {
        background: linear-gradient(55deg, $main-color 75%, $corner-color 0%);
    }

    @include media('>=tablet') {
        background: linear-gradient(55deg, $main-color 75%, $corner-color 0%);
    }

    @include media('>=between') {
        background: linear-gradient(45deg, $main-color 75%, $corner-color 0%);
    }

    @include media('>=small') {
        background: linear-gradient(55deg, $main-color 75%, $corner-color 0%);
    }

    @include media('>=desktop') {
        background: linear-gradient(45deg, $main-color 75%, $corner-color 0%);
    }

    @include media('>=large') {
        background: linear-gradient(45deg, $main-color 75%, $corner-color 0%);
    }
}

@mixin pathway-image-cards(
    $background-color: $color-gray2,
    $card-text-color: $color-nearblack,
    $card-text-hover-color: $color-nearblack,
    $card-background-hover-color: $color-gray1,
    $card-heading-color: $color-gray7,
    $card-heading-hover-color: $color-nearblack,
    $section-title-color: $color-nearblack,
    $outline-color: $color-nearblack
) {
    background-color: $background-color;
    padding: $measure--small4;

    h2,
    h3 {
        color: $section-title-color;
        text-align: center;
    }

    a {
        display: block;
        border: none;
        transition: none;
        color: $card-text-color;
        position: relative;

        &:visited {
            color: $card-text-color;
        }

        &:hover,
        &:active,
        &:focus {
            background-image: none;
            background-color: transparent;
            border: none;
            outline: none;
            color: $card-text-hover-color;
            z-index: z-index('above');

            p,
            span {
                color: $card-text-hover-color;
            }
        }

        &:focus,
        &:active {
            outline: none;

            &:after {
                @include outline-properties($outline-color);
            }
        }

        p,
        span {
            color: $card-text-color;
        }

        @include thumbnail-gradient-scale;
        @include background-color-and-scale($card-background-hover-color, 1.1);
        @include underline-transition--subelement(
            $card-heading-color,
            $card-heading-hover-color,
            $border-width-thick,
            $color-nearblack,
            'underline-transition__title',
            $show-outline: false
        );

        img {
            transition: transform $transition-duration ease-in;
            width: 100%;

            @include media('reducemotion') {
                transition: none;
            }
        }
    }
}

@mixin dropdown-filter-select(
    $border-color: $color-pink90,
    $color: $color-nearblack,
    $background-color: transparent
) {
    background-color: $background-color;
    color: $color;
    border: $border-width-default $border-style-solid $border-color;
}
