////
/// Global Header
/// @group patterns
////
.global-header {
    background-color: $color-purewhite;
    position: relative;
    z-index: z-index('unveiled');

    @include media('print') {
        display: none;
    }

    .global-header__content {
        @include media('<small') {
            align-items: center;
            display: flex;
            flex-direction: row;
            flex-wrap: nowrap;
            justify-content: space-between;
        }

        @include media('>=small') {
            @include standard-box();
            @include standard-grid();

            grid-template-rows: repeat(3, auto);
        }

        @include media('>=desktop') {
            grid-row-gap: $measure--basic;
            grid-template-rows: repeat(2, auto);
        }
    }
}

.header-logo {
    @include media('<=tiny') {
        margin-left: $measure--half;
        margin-right: $measure--half;
    }

    @include media('>tiny', '<small') {
        margin-left: $measure--small2;
        margin-right: $measure--small2;
        max-width: $measure--medium11;
    }

    @include media('>=small') {
        grid-column-end: span 3;
        grid-column-start: 1;
        grid-row-end: span 2;
        grid-row-start: 1;
    }

    a {
        @include anchor-img();

        display: inline-block;
        margin-bottom: $measure--small2;
        margin-top: $measure--small2;

        .logo {
            width: $measure--huge5;
        }
    }
}

.global-header__utility {
    display: none;

    @include media('>=small') {
        display: block;
        grid-column-end: 22;
        grid-column-start: 4;
        grid-row-end: 1;
        grid-row-start: 1;
    }

    ul {
        align-items: center;
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        height: 100%;
        justify-content: flex-end;
        list-style: none;
        margin-left: 0;
        margin-top: 0;
        max-width: unset;

        li {
            @include smalltype();

            &:not(:last-of-type) {
                &:after {
                    color: $color-gray4;
                    content: '|';
                    margin-left: $measure--threequarters;
                    margin-right: $measure--threequarters;
                }
            }

            a {
                @include underline-transition--subelement(
                    $color-gray7,
                    $color-gray10,
                    $border-width-thick,
                    $color-nearblack,
                    'global-header__utility__text'
                );

                height: 100%;

                .global-header__utility__icon {
                    color: $color-gray7;
                    margin-left: $measure--quarter;
                    top: -#{$border-width-default};
                }

                &:hover .global-header__utility__icon {
                    @include media('hover') {
                        color: $color-gray10;
                    }
                }
            }
        }
    }
}

.global-header__search {
    grid-row-start: 1;
    grid-column-start: 22;
    grid-column-end: span 3;

    @include media('<=tiny') {
        margin-right: $measure--half;
    }

    @include media('>tiny', '<small') {
        margin-right: $measure--small2;
    }

    @include media('>=small') {
        grid-row-end: 1;
    }

    > a,
    > button {
        @include media('<small') {
            @include h6style();
            @include cta--border--subelement(
                transparent,
                $color-nearblack,
                $color-gray7,
                $color-nearblack,
                $color-purewhite,
                $color-gray7,
                $color-purewhite,
                square,
                'global-header__search__text'
            );

            display: flex;
            height: unset;
            margin-top: 0;
        }

        @include media('<=mobile') {
            @include smalltype();
        }

        @include media('>=small') {
            @include h6style();
            @include cta--basic--subelement(
                $color-purewhite,
                $color-red50,
                $color-purewhite,
                $color-red70,
                $color-purewhite,
                square,
                'global-header__search__text'
            );

            display: flex;
            height: 100%;
            margin-top: 0;
        }

        @include media('>=desktop') {
            @include h5style();

            margin-top: 0;
        }

        align-items: center;
        flex-direction: row;
        flex-wrap: nowrap;
        justify-content: center;
        min-height: $minimum-control-height;
        width: 100%;

        .global-header__search__icon {
            margin-left: $measure--half;
        }
    }
}

.navigation--primary {
    display: none;

    @include media('>=small') {
        display: block;
        grid-column-end: span 24;
        grid-column-start: 1;
        grid-row-end: 3;
        grid-row-start: 3;
        padding-bottom: $measure--small2;
    }

    @include media('>=desktop') {
        grid-column-end: span 21;
        grid-column-start: 4;
        grid-row-end: 2;
        grid-row-start: 2;
    }

    > ul {
        align-items: flex-end;
        height: 100%;
    }
}

.promo-strap {
    background-color: $color-purple90;
    background: linear-gradient(90deg, $color-purple90 0%, $color-red60 100%);
    color: $color-purewhite;
    padding-bottom: $measure--basic;
    padding-top: $measure--basic;

    .promo-strap__content {
        @include h6style();
        @include standard-box();

        font-weight: $font-weight-normal;
        margin-top: 0;
        text-align: center;

        sup,
        sub {
            font-size: $font-size--super;
            line-height: $font-size--super;
        }

        a {
            @include underline-transition(
                $inactive-color: $color-purewhite,
                $hover-color: $color-purewhite,
                $underline-width: $border-width-default,
                $outline: $color-purewhite
            );
        }
    }
}
