.card--findus {
    a {
        @include anchor-reset-properties;
        @include box-shadow;
        color: $color-nearblack;
        overflow: hidden;
        display: block;
        background-image: none;

        &:visited {
            color: $color-nearblack;
        }

        &:hover {
            @include media('hover') {
                color: $color-nearblack;
                background-image: none;
                border-bottom: none;
            }

            .card--findus__map {
                transform: scale(1.04);

                &:before {
                    background-color: rgba($color-gray11, 0.1);
                }
            }

            .card--findus__content__name {
                background-repeat: no-repeat;
                color: $color-text--dark;

                background-size: 100% 100%;
                background-image: linear-gradient(
                    120deg,
                    $color-inline-link 0%,
                    $color-inline-link 100%
                );
            }
        }

        &:active {
            .card--findus__map {
                transform: scale(1.04);

                &:before {
                    background-color: rgba($color-gray11, 0.1);
                }
            }
        }

        &:active,
        &:focus {
            color: $color-nearblack;
            background-image: none;
            border-bottom: none;
            background-color: transparent;
        }
    }
}

.card--findus--figure {
    a {
        @include media('>tablet') {
            display: flex;
        }
    }
}

.card--findus__map {
    position: relative;
    transition: all 0.3s ease-in-out;
    flex: 1;

    &:before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        background: transparent;
        transition: background-color 0.3s ease-in-out;
    }

    img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;

        @include media('<tablet') {
            max-height: 14rem;
        }
    }
}

.card--findus__content {
    padding: $measure--small2;
    flex: 1;
}

.card--findus__content__header {
    @include betatype;
    font-weight: $font-weight-medium;
    color: $color-gray7;
    border-bottom: $border-width-hairline $border-style-solid $color-gray2;
    padding-bottom: $measure--half;
}

.card--findus__content__name {
    font-weight: $font-weight-bold;
    @include anchor-hover-background-settings;
    color: $color-inline-link;
    border-bottom-color: $color-inline-link;
    border-bottom-style: $border-style-solid;
    border-bottom-width: $border-width-default;
    display: inline-block;
    margin-top: $measure--small1;
}

.card--findus__content__address {
    display: flex;

    p {
        margin-left: $measure--basic;
    }

    .icon {
        color: $color-red50;
        @include alphatype();
        margin-top: $content-element-margin;
    }
}
