// <Desktop-specific layout - secondary navigation never present
main > .lifelong-learning {
    @include media('<small') {
        .embedded-video {
            .youtube-video {
                margin-top: $measure--small1;
            }
        }

        .embedded-video-details__heading {
            &:after {
                margin-left: auto;
                margin-right: auto;
            }
        }
    }
}

// Desktop-specific layout
main {
    @include media('>=small') {
        .embedded-video {
            display: flex;
            flex-direction: row;
            flex-wrap: nowrap;

            .youtube-video {
                width: 100%;
            }
        }

        .embedded-video--with-heading {
            .embedded-video-details {
                width: calc(35% - #{$measure--small4});
                margin-right: $measure--small4;
            }

            .youtube-video {
                width: 65%;
            }
        }

        .youtube-video {
            margin: 0;
        }
    }
}

