// Generic styles
.embedded-video {
    padding-bottom: $measure--small2;
    padding-left: $measure--small2;
    padding-right: $measure--small2;
    padding-top: $measure--small2;

    @include media('>between') {
        padding-bottom: $measure--small4;
        padding-left: $measure--small4;
        padding-right: $measure--small4;
        padding-top: $measure--small4;
    }

    .embedded-video-details {
        align-items: center;
        display: flex;
        flex-direction: column;
        justify-content: center;
    }

    .embedded-video-details__heading {
        margin-top: 0;

        &:after {
            background: $color-pink80;
            content: '';
            display: block;
            height: $measure--quarter;
            margin-bottom: $measure--small4;
            margin-top: $measure--small4;
            width: $measure--large2;

            @include media('<small') {
                margin-bottom: $measure--small1;
                margin-top: $measure--small1;
            }
        }
    }
}

.youtube-video__wrapper {
    height: 0;
    padding-bottom: 56.25%;
    position: relative;
    width: 100%;

    iframe {
        height: 100%;
        left: 0;
        margin: 0;
        position: absolute;
        top: 0;
        width: 100%;
    }
}

.youtube-video__caption-element--text,
.youtube-video__caption-element--time {
    display: block;
}
