.promo-box {
    background-color: $color-red50;
    background-image: linear-gradient(to right, $color-red50, $color-pink80);
    color: $color-purewhite;
    padding-top: $measure--threequarters;
    padding-bottom: $measure--small4;
    @include default-padding;

    @include media('>between') {
        padding-left: $measure--medium11;
        padding-right: $measure--medium11;
    }
}

.promo-box__wrapper {
    display: block;
    padding-top: $measure--small1;

    &:nth-of-type(2) {
        margin-top: $measure--small5;
    }

    @include media('>tablet') {
        display: flex;
    }
}

.promo-box__card {
    &:nth-of-type(2) {
        margin-top: $measure--small5;
    }

    @include media('>tablet') {
        &:nth-of-type(2) {
            margin-top: 0;
        }
    }

    &:first-of-type {
        margin-right: $measure--small4;
    }
}

.promo-box__card__body {
    display: flex;

    img {
        min-width: $measure--medium4;
        max-width: $measure--medium4;
        min-height: $measure--medium4;
        max-height: $measure--medium4;
        display: block;
        margin-right: $measure--small1;
    }
}
