// <Desktop-specific layout - secondary navigation never present
main > article {
    @include media('<small') {
        .banner {
            @include x-column-grid(24);

            display: grid;

            .banner__image {
                &,
                & + .banner__overlay {
                    grid-column: 1 / span 24;
                    grid-row: 1;
                }
            }
        }
    }
}

// Desktop-specific layout
main {
    @include media('>=small') {
        > article {
            .banner {
                @include x-column-grid(24);

                .banner__image {
                    &,
                    & + .banner__overlay {
                        grid-column: 1 / span 24;
                        grid-row: 1;
                    }
                }
            }
        }
    }
}

// Desktop specific layout if secondary navigation is present
main {
    @include media('>=small') {
        .navigation--secondary {
            & + article {
                .banner {
                    @include content-grid();

                    .banner__image {
                        &,
                        & + .banner__overlay {
                            grid-column: 1 / span 18;
                            grid-row: 1;
                        }
                    }
                }
            }
        }
    }
}

// Generic styles
.banner {
    position: relative;
    overflow: hidden;
    grid-template-rows: auto;
}

.banner__image {
    height: 100%;

    @include media('<=tablet') {
        max-height: $banner-mobile-image-max-height;
    }

    @include media('>tablet') {
        max-height: $banner-tablet-image-max-height;
    }

    @include media('>=between') {
        max-height: $banner-between-image-max-height;
    }

    @include media('>=desktop') {
        max-height: $banner-desktop-image-max-height;
    }

    @include media('>=4K') {
        max-height: $banner-4k-image-max-height;
    }

    @include media('>=8K') {
        max-height: $banner-8k-image-max-height;
    }

    .hero-image {
        width: 100%;
        overflow: hidden;
        display: flex;

        img {
            width: 100%;
            object-fit: cover;
            align-self: auto;
        }
    }
}

.banner__overlay {
    background: linear-gradient(to top, $color-gray9, transparent 100%);
    width: 100%;
    height: 100%;

    // If grid layout is required, an extra wrapping element is needed with flex properties. This allows you to then have a grid nested inside.
    &.flex-wrapper--banner__overlay {
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        .banner__overlay__heading--grid {
            @include content-grid();
            @include media('<=small') {
                @include standard-box();
            }
        }
    }
}

.banner__overlay__heading {
    @include text-shadow();
    @include default-margin();
    @include default-padding();

    color: $color-purewhite;
    display: flex;
    flex-direction: column;
    height: 100%;
    justify-content: flex-end;
    padding-bottom: $measure--small4;

    @include media('<=between') {
        padding-bottom: $measure--basic;
    }
}

.banner--center {
    .banner__overlay {
        .banner__overlay__heading {
            text-align: center;
        }
    }

    h1,
    p {
        max-width: unset;
    }
}
