////
/// Slider
/// Left/right arrows - navigate to previous/next content in listing
/// @group patterns
////

.slider {
    display: flex;
    flex-direction: column;
}

.slider__active-slide {
    display: flex;
}

.slider__slide {
    display: none;
}

.slider__controls__wrap {
    grid-column: 6 / 14;
    display: flex;
    flex-direction: row-reverse;
    margin: $measure--basic 0;
}

.slider__controls {
    display: flex;
    align-self: flex-end;
}

.slider__controls__progress {
    color: $color-gray7;
    margin: $measure--basic $measure--small4 0 0;
    font-size: $font-size--extrasmalltype;
    font-weight: $font-weight-book;
}

.slide__controls__progress__active {
    color: $color-red50;
}

.slider__controls__buttons__next,
.slider__controls__buttons__prev {
    border: $border-width-hairline $border-style-solid $color-gray10;
    padding: $measure--threequarters $measure--half;
    background: $color-purewhite;
    margin-left: $measure--basic;
    @include anchor-default-outline($color-outline);

    &:hover {
        @include media('hover') {
            background: $color-pink90;
            color: $color-purewhite;
        }
    }

    &:focus,
    &:active {
        color: $color-purewhite;
        background: $color-pink90;
        outline-offset: 0;
    }

    &:before {
        font-weight: $font-weight-normal;
    }
}

.l .slider__controls__buttons__disabled {
    border: none;
    color: $color-gray10;
    opacity: 0.7;

    &:hover {
        background: $color-purewhite;
        color: $color-gray10;
    }
}
