.research-card-block {
    background: $color-gray1;

    @include media('<between') {
        background: none;
    }

    @include media('>between') {
        padding: $measure--small4;
    }
}

.card--research {
    padding: $measure--basic;

    @include media('>mobile') {
        padding: $measure--small4;
    }

    &:hover,
    &:focus-within {
        transition: background-color 0.6s;
        background-color: $color-purewhite;
    }
}

.card--research__title-link {
    @include h5style();
}

.card--research__research-leader,
.card--research__research-centre {
    margin-top: $measure--small1;

    span {
        display: block;
        font-weight: $font-weight-bold;
    }
}

.card--research__status {
    margin-top: $measure--small2;
    color: $color-gray7;

    span:first-child {
        font-weight: $font-weight-bold;
    }
}
