.card--testimonial {
    display: flex;
    flex-direction: column;
    position: relative;

    @include media('>tablet') {
        flex-direction: row;
    }

    .card--testimonial__photo {
        margin-top: $measure--small4;

        @include media('<=tablet') {
            position: absolute;
            right: 0;
            bottom: -$measure--half;
            margin-top: -$measure--medium3;
        }

        @include media('>tablet') {
            align-self: start;
            margin-left: $measure--basic;
            margin-right: $measure--small4;
        }

        img {
            width: $measure--large4;
            max-width: none;
            @include media('<=tablet') {
                width: $measure--medium7;
            }
        }
    }

    .card--testimonial__text {
        @include media('<=tablet') {
            margin-bottom: 0;
            grid-row: 2;
            grid-column: -3/-1;
        }
        .card--testimonial__quote {
            margin-top: $measure--small4;
            position: relative;
            padding-left: $measure--threequarters;

            p {
                @include standfirst();
                /**
                 * Increasing the font size of the quotation marks means that the
                 * lines containing them are slightly taller, resulting in uneven
                 * spacing.
                 *
                 * Increase the spacing throughout to make space and so its at
                 * equal throughout.
                 **/
                line-height: $line-height-ratio--override--zeta;
                color: $color-gray10;

                &:first-child {
                    margin-top: 0;
                }
            }

            &:before,
            :last-child:after {
                @include standfirst();
                @include zetatype();
                color: $color-pink90;
                display: inline;
                /**
                 * Quotation mark line height equal to other lines, even though
                 * the font size is greater.
                 **/
                line-height: $line-height-ratio--override--alpha;
            }

            &:before {
                content: '';
                left: 0;
                position: absolute;
                top: #{-$measure--half};
            }

            :last-child:after {
                content: '';
            }
        }

        .card--testimonial__author {
            @include smalltype();
            color: $color-gray7;
            font-weight: $font-weight-bold;
            padding: $measure--small4 $measure--basic 0 0;
            // Width value needed to prevent image overlap on smaller viewports
            width: 70%;
        }
    }
}
