main {
    @include media('>=tablet') {
        > article {
            .finder {
                .finder__results__list {
                    @include content-grid();

                    grid-auto-rows: auto;

                    .finder__results__card {
                        grid-column: 3 / -3;
                    }
                }
            }
        }
    }
}

// Desktop-specific layout
main {
    @include media('>=small') {
        > article {
            .banner-text .banner-text__content {
                @include x-column-grid(24);

                display: grid;
                margin: 0;
                padding: 0;

                h1,
                p {
                    grid-column: 3 / -3;
                }
            }

            .finder {
                @include x-column-grid(24);

                display: grid;

                .finder__query,
                .finder__filters--desktop,
                .finder__results {
                    grid-column: 3 / -3;
                }

                .finder__query {
                    margin-left: 0;
                    margin-right: 0;
                }

                .finder__filters--desktop .finder__filters__heading {
                    margin: 0;
                    padding-left: 0;
                    padding-right: 0;
                }

                .finder__filters--desktop .finder__filters__wrapper--filters {
                    margin: 0;
                }

                .finder__results__summary {
                    margin: 0;
                }

                .finder__results__list {
                    @include x-column-grid(20);

                    .finder__results__card {
                        grid-column: 3 / -3;
                    }
                }
            }
        }
    }

    @include media('>=desktop') {
        > article {
            .banner-text .banner-text__content {
                h1,
                p {
                    grid-column: 4 / -4;
                }
            }

            .finder {
                .finder__query,
                .finder__filters--desktop,
                .finder__results {
                    grid-column: 4 / -4;
                }

                .finder__results__list {
                    @include content-grid();

                    .finder__results__card {
                        grid-column: 3 / -3;
                    }
                }
            }
        }
    }
}

// Desktop specific layout if secondary navigation is present
main {
    @include media('>=small') {
        .navigation--secondary {
            & + article {
                .banner-text .banner-text__content {
                    @include standard-box();
                    display: block;
                }

                .finder {
                    display: block;

                    .finder__query {
                        @include default-margin();
                    }

                    .finder__filters--desktop .finder__filters__heading {
                        @include default-margin();
                    }

                    .finder__filters--desktop
                        .finder__filters__wrapper--filters {
                        @include default-margin();
                    }

                    .finder__results__summary {
                        @include default-margin();
                    }

                    .finder__results__list {
                        @include content-grid();

                        .finder__results__card {
                            grid-column: 3 / -3;
                        }
                    }
                }
            }
        }
    }
}

.finder__query {
    background-color: $color-red50;
    padding: $measure--small2;
    position: relative;

    & > div {
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        height: 100%;
    }

    @include media('>=tablet') {
        margin-left: $measure--small2;
        margin-right: $measure--small2;
    }
}

.finder__query__input {
    @include font-family('default');
    @include body-font-features();
    @include alphatype();

    @include media('>=tablet') {
        @include gammatype();
    }

    background-color: $color-purewhite;
    display: flex;
    flex-grow: 1;
    font-weight: $font-weight-text;
    padding: 0;
    position: relative;

    &:focus-within {
        @include outline-properties($color-purewhite);
    }

    input {
        @include font-family('default');
        @include body-font-features();
        @include gammatype();

        border: $border-width-hairline solid transparent;
        font-style: normal;
        height: 100%;
        min-width: 0; // Constrains width in Firefox
        padding-bottom: $measure--half;
        padding-left: $measure--half;
        padding-right: $measure--medium5;
        padding-top: $measure--half;
        width: 100%;

        &:focus {
            color: $color-gray10;
            font-style: normal;
            font-weight: $font-weight-medium;
            outline: 0;

            &::placeholder,
            &::-ms-input-placeholder {
                opacity: 0;
            }
        }

        &::placeholder {
            font-style: italic;
        }

        &:placeholder-shown {
            color: $color-gray7;
        }
    }

    .finder__clear {
        cursor: pointer;
        height: $measure--small4;
        position: absolute;
        right: $measure--basic;
        top: $measure--half;
        width: $measure--small4;

        &:focus {
            @include outline-properties();
        }

        .finder__clear__text {
            @include sr-only();
        }

        .icon {
            background-color: $color-gray1;
            border-radius: 50%;
            color: $color-gray7;
            height: $measure--small4;
            position: relative;
            width: $measure--small4;

            &:hover {
                @include media('hover') {
                    cursor: pointer;
                }
            }

            @include media('<tablet') {
                width: $measure--small2;
                height: $measure--small2;
            }

            &:before {
                display: block;
                margin-bottom: $measure--threequarters;
                margin-top: $measure--half;
            }
        }

        .finder__icon--close {
            background-color: transparent;
            left: 0;
            position: relative;
            top: $measure--quarter;

            @include media('>=tablet') {
                left: $measure--quarter;
                top: $measure--half;
            }
        }
    }
}

.finder__query__icon--wrapper {
    background-color: $color-purewhite;
    padding: #{$measure--quarter + $border-width-hairline} $measure--half $measure--half
        $measure--basic;
}

.finder__query__submit {
    @include gammatype();
    @include cta--basic(
        $color-purewhite,
        $color-pink90,
        $color-purewhite,
        $color-pink70,
        $color-purewhite,
        round
    );

    flex-shrink: 0;
    font-weight: $font-weight-book;
    margin-left: $measure--basic;

    &:hover {
        cursor: pointer;
    }

    @include media('>=tablet') {
        margin-left: $measure--small4;
    }

    @include media('<tablet') {
        padding: $measure--basic $measure--small1;

        &:hover {
            padding: $measure--basic $measure--small1;
        }
    }

    span {
        margin: auto;
    }

    .finder__query__submit__text {
        @include media('<tablet') {
            @include screenreader-only();
        }
    }

    .finder__query__submit__icon {
        font-weight: $font-weight-medium;
        padding: 0 $measure--half 0 0;

        @include media('<tablet') {
            display: block;
            padding: 0;
        }
    }
}

.finder__didyoumean__button {
    @include anchor-inline(
        $color-inline-link,
        $color-inline-link,
        $color-text--dark,
        $color-outline
    );
}

.finder__filter {
    background-color: $color-purewhite;

    legend {
        @include h3style();
        position: relative;
        display: block;
        width: 100%;
        background-color: $color-purewhite;
        padding: 0;

        button {
            background-image: none;
            border-bottom-color: $color-purewhite;
            padding: $measure--threequarters $measure--small2;
            width: 100%;

            &:active,
            &:focus {
                outline-offset: -$measure--quarter;
            }

            &:hover {
                .icon {
                    color: $color-red50;
                    transition: color $transition-duration--long;
                }
            }

            & > span {
                color: $color-nearblack;
                display: flex;
                flex-direction: row;
                flex-wrap: nowrap;
                justify-content: space-between;
            }
        }

        button[aria-expanded='true'] {
            .icon {
                color: $color-red50;
            }
        }
    }

    label {
        cursor: pointer;
        width: 100%;
    }

    input:not(:disabled) {
        cursor: pointer;
    }

    input[type='radio'] {
        align-self: flex-start;
        display: block;
        margin-right: $measure--basic;
        margin-top: $measure--half;
    }

    legend[data-open='true'] {
        &:after {
            border-top: $border-width-hairline solid $color-gray3;
            bottom: 0;
            content: '';
            left: $measure--small2;
            position: absolute;
            width: calc(100% - $measure--medium3);
        }
    }

    legend[data-open='true'] + ul {
        display: block;
    }

    legend[data-open='false'] + ul {
        display: none;
    }

    ul {
        background-color: $color-purewhite;
        list-style-type: none;
        margin-left: 0;
        margin-top: 0;
        max-width: unset;
        padding: $measure--threequarters $measure--small2;
        position: relative;
    }

    li {
        align-items: center;
        display: flex;
        flex-direction: row;
    }
}

.finder__radio {
    margin-top: $measure--quarter;
}

.finder__togglebox {
    background-color: $color-purewhite;
    padding: $measure--basic;
}

.finder__toggle {
    label {
        display: flex;
        font-weight: bold;
    }

    input {
        height: 0;
        opacity: 0;
        width: 0;

        &:checked + .toggle:before {
            transform: translateX(
                $measure--threequarters - $border-width-default
            );
        }

        &:focus + .toggle {
            outline-color: $color-gray11;
            outline-offset: $border-width-thick;
            outline-style: dotted;
            outline-width: $border-width-default;
        }
    }
}

.toggle {
    background-color: $color-gray1;
    border-radius: $measure--medium3;
    border: $border-width-hairline solid $color-gray7;
    display: inline-block;
    height: $measure--small1 + $border-width-default;
    position: relative;
    transition: background-color $transition-duration;
    width: $measure--small4;

    &:before {
        background-color: $color-purewhite;
        border-radius: 50%;
        border: $border-width-hairline solid;
        content: '';
        height: $measure--small1;
        position: absolute;
        transition: transform 0.3s;
        width: $measure--small1;
    }
}

.toggle--open {
    background-color: $color-yellow40;
}

.finder__query__suggestions {
    background-color: $color-purewhite;
    border-radius: 0 0 $measure--half $measure--half;
    border-top: 0;
    border: $border-width-hairline $border-style-solid $color-gray2;
    box-shadow: rgba(0, 0, 0, 0.25) 0 $measure--basic $measure--medium3 -$measure--threequarters;
    color: $color-purewhite;
    font-weight: $font-weight-bold;
    left: 0;
    list-style: none;
    margin-left: 0;
    max-width: unset;
    position: absolute;
    top: $measure--medium1;
    width: 100%;
    z-index: z-index('above');

    li {
        font-weight: $font-weight-text;
        margin-top: 0;

        button {
            color: $color-text;
            height: 100%;
            padding: $measure--half $measure--small4 $measure--half
                $measure--small4;
            text-align: left;
            width: 100%;

            &:focus {
                outline-color: $color-nearblack;
                outline-offset: (-$border-width-default);
                outline-style: $border-style-dotted;
                outline-width: $border-width-default;
                text-decoration: underline;
            }

            &:active {
                @include outline-properties();

                outline-offset: 0;
            }

            &:hover {
                background-color: $color-linkblue;
                color: $color-purewhite;
                text-decoration: underline;
            }

            .finder__suggestion {
                font-weight: $font-weight-bold;
            }
        }
    }
}

.finder__query__suggestions.hide {
    display: none;
}

.finder__filters {
    .finder__clear {
        @include media('>=tablet') {
            @include alphatype();
            @include cta--border(
                $background: $color-purewhite,
                $color: $color-red50,
                $border: $color-red50,
                $hover-color: $color-purewhite,
                $hover-border: $color-red50,
                $outline: $color-nearblack,
                $corners: round
            );

            height: $measure--medium4;
            margin-right: $measure--basic;
        }
    }

    fieldset:last-child {
        .finder__select {
            margin-bottom: $measure--basic;
        }
    }
}

.finder__filters--mobile .finder__filters {
    margin-bottom: $measure--medium3;
}

.finder__filters--desktop .finder__filters {
    display: none;

    @include media('>=tablet') {
        background-color: $color-purewhite;
        display: block;

        .finder__filter,
        .finder__filters__clear--desktop {
            width: calc(33% - #{$measure--small2});
            margin-right: $measure--small2;

            @include media('>=desktop') {
                width: calc(25% - #{$measure--small2});
            }
        }

        .finder__filters__clear--desktop {
            margin-bottom: $measure--basic;
            margin-right: $measure--basic;
            margin-top: $measure--threequarters;
        }
    }
}

.finder__filters__wrapper--filters {
    @include media('<tablet') {
        background-color: $color-purewhite;
        padding-left: $measure--small2;
        padding-right: $measure--small2;
    }

    @include media('>=tablet') {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        padding-left: $measure--small2;
    }
}

.finder__filters__heading {
    @include deltatype();

    background-color: $color-gray3;
    display: flex;
    justify-content: space-between;
    margin-top: 0;
    padding: $measure--threequarters $measure--small2;

    @include media('>mobile') {
        justify-content: center;
    }

    @include media('>=tablet') {
        background-color: transparent;
        justify-content: flex-start;
    }

    .icon {
        margin-right: $measure--half;
        margin-top: $measure--quarter;
    }
}

.finder__filters .finder__clear {
    display: none;
}

.finder__filters--mobile {
    &[data-open='false'] {
        padding-left: $measure--small2;
        padding-right: $measure--small2;
    }

    .finder__filters__heading {
        padding: 0;

        > .icon,
        > .finder__filters__heading__text {
            padding: $measure--half;
        }
    }

    .finder__filters .finder__clear {
        @include cta--basic--subelement(
            $color: $color-purewhite,
            $background: $color-red50,
            $hover-color: $color-purewhite,
            $hover-background: $color-red70,
            $outline: $color-purewhite,
            $corners: square,
            $class: 'finder__clear__text'
        );
        @include media('>mobile') {
            margin-left: $measure--medium1;
        }

        display: block;

        .icon {
            display: none;
        }
    }

    .finder__filters__clear--desktop .finder__clear {
        display: none;
    }
}

.finder__filters__clear--desktop .finder__clear {
    display: block;
}

.finder__filters__heading__btn-icon {
    display: flex;
}

.finder__filters--desktop .finder__filters label {
    @include sr-only();
}

.finder__filters__nofilters {
    display: block;
    margin-bottom: $measure--basic;
}

.finder__filters__clear--desktop,
.finder__filter {
    + .finder__filters__nofilters {
        display: none;
    }
}

.finder__results__summary__heading {
    @include h3style();
    text-align: left;

    @include media('<=mobile') {
        @include betatype();
    }

    span {
        @include content-style();

        span {
            color: $color-red50;
            font-weight: $font-weight-bold;

            @include media('<=mobile') {
                font-weight: $font-weight-medium;
            }
        }
    }
}

.finder__filters--mobile {
    display: block;

    .finder__filters--mobile__apply {
        @include cta--basic--subelement(
            $color: $color-purewhite,
            $background: $color-red50,
            $hover-color: $color-purewhite,
            $hover-background: $color-red70,
            $outline: $color-purewhite,
            $corners: square,
            $class: 'finder__filters--mobile__apply__text'
        );
        @include gammatype();
    }

    .finder__filters--mobile__toggle {
        @include cta--border(
            $background: $color-purewhite,
            $color: $color-nearblack,
            $border: $color-nearblack,
            $hover-color: $color-purewhite,
            $hover-border: $color-nearblack,
            $outline: $color-nearblack,
            $corners: square
        );
        @include gammatype();

        margin-top: $heading-margin;
    }

    .finder__filters--mobile__toggle[data-filters-applied='true'] {
        border-color: $color-red50;

        .finder__filters--mobile__toggle__filterNo {
            color: $color-red50;
        }
    }

    @include media('>=tablet') {
        display: none;
    }

    &[data-open='true'] {
        background-color: $color-purewhite;
        bottom: 0;
        height: 100vh;
        left: 0;
        position: fixed;
        right: 0;
        top: 0;
        width: 100vw;
        z-index: z-index('modal');

        .finder__filters--mobile__filters {
            -webkit-overflow-scrolling: touch;
            height: 100vh;
            overflow-y: scroll;
            width: 100vw;
        }

        .finder__filters--mobile__filters__content {
            padding-bottom: $measure--medium9;
        }

        .finder__filters {
            @include media('>=tablet') {
                padding-left: $measure--small2;
                padding-right: $measure--small2;
            }
        }

        .wrapper--finder__filters--mobile__apply {
            bottom: 0;
            position: fixed;
            width: 100%;
        }

        .finder__filters--mobile__apply {
            background-color: $color-red50;
            border: 0;
            color: $color-purewhite;
            margin-top: $measure--basic;
            padding-bottom: $measure--basic;
            padding-top: $measure--basic;
            width: 100%;

            &:hover,
            &:active {
                background-color: $color-red70;
                outline-color: $color-purewhite;
                outline-offset: -$border-width-thick;
            }

            &:focus {
                outline-color: $color-purewhite;
                outline-offset: -$border-width-thick;
            }

            .fa-chevron-left {
                border-bottom: none;
                left: $measure--basic;
                padding: $measure--quarter 0;
                position: absolute;
            }

            & > span {
                border-bottom: $border-width-default $border-style-solid
                    $color-purewhite;
            }
        }

        .finder__filters,
        .finder__filters--mobile__apply {
            display: block;
        }

        .finder__filters--mobile__toggle {
            display: none;
        }
    }

    &[data-open='false'] {
        text-align: right;

        .finder__filters,
        .finder__filters--mobile__apply {
            display: none;
        }

        .finder__filters--mobile__toggle {
            display: inline-block;
        }
    }
}

.finder__results__summary {
    border-bottom: $border-width-hairline $border-style-solid $color-gray4;
    padding-bottom: $measure--basic;

    button {
        @include anchor-inline();
    }
}

.finder .finder__results {
    margin-left: $measure--small2;
    margin-right: $measure--small2;

    @include media('>=tablet') {
        margin-left: 0;
        margin-right: 0;

        .finder__results__summary,
        .finder__results__updating,
        .finder__results__didyoumean,
        .pagination__wrapper {
            margin-left: $measure--small2;
            margin-right: $measure--small2;
        }
    }

    .pagination__wrapper {
        border-top: $border-width-hairline $border-style-solid $color-gray4;
        max-width: unset;
    }

    .pagination__controls {
        justify-content: center;
    }
}

.finder__results__list {
    list-style-type: none;
    margin-left: 0;
    max-width: unset;
}

.finder .highlightText {
    background-color: $color-red10;
}

.finder__results__updating {
    @include gammatype();

    margin-bottom: $heading-margin;
    margin-top: $heading-margin;

    .icon {
        color: $color-red50;
    }
}

.finder__results__card {
    border-bottom: $border-width-hairline solid $color-gray4;
    margin-top: $measure--small4;
    padding-bottom: $measure--small4;
    position: relative;
    z-index: z-index('default');

    &:last-of-type {
        border-bottom: 0;
    }

    @include media('>=tablet') {
        .thumbnail__image-container {
            margin-right: $measure--basic;
        }
    }

    a {
        @include anchor-reset-properties();
        @include underline-transition--subelement(
            $color-gray7,
            $color-gray10,
            $border-width-thick,
            'underline-transition__title'
        );
        @include background-color-and-scale;
        @include card-shared-properties;
        @include thumbnail-gradient-scale;

        &:hover {
            @include media('hover') {
                background-color: $color-gray1;
                background-image: none;
                outline-color: transparent;
                outline-width: 0;

                &:after {
                    background-color: $color-gray1;
                }
            }
        }

        &:active {
            background-color: $color-gray1;
            background-image: none;
            outline-color: transparent;
            outline-width: 0;
        }

        &:focus {
            background-image: none;
            outline-color: transparent;
            outline-width: 0;
        }

        display: flex;
        flex-direction: column;
        padding-left: 0;
        padding-right: 0;

        @include media('>=tablet') {
            flex-direction: row;
        }

        .underline-transition__title {
            margin-top: $heading-margin;

            @include media('>=tablet') {
                margin-top: 0;
            }
        }

        .finder__results__card__description {
            @include content-style();
            @include smalltype();

            color: $color-red50;
            margin-top: $measure--half;
        }

        .finder__results__card__tag {
            @include smalltype();

            color: $color-gray8;
            margin-top: $content-element-margin;
            max-width: unset;

            .icon {
                color: $color-red50;
                margin-right: $measure--half;
            }
        }
    }
}

.finder__results__contact {
    margin-top: 0;

    a {
        @include anchor-reset-properties();
        @include anchor-inline();

        display: inline;
        margin: 0;
        padding: 0;

        &:after {
            content: unset;
        }
    }

    .finder__results__card__description {
        @include content-style();
        @include smalltype();

        color: $color-red50;
        margin-top: $measure--half;
    }

    .icon,
    .finder__results__card__description {
        color: $color-red50;
    }

    .finder__results__card__tag {
        @include smalltype();

        color: $color-gray8;
        margin-top: $content-element-margin;

        .icon {
            margin-right: $measure--half;
        }
    }
}

.finder__results__bestbet {
    a .finder__results__card__description {
        color: $color-text;
    }
}

.finder__select {
    color: $color-nearblack;
    height: auto;
    margin-top: $measure--basic;
    position: relative;

    @include media('<tablet') {
        margin-bottom: $measure--half;
    }

    select {
        @include font-family('default');
        @include body-font-features();
        @include alphatype();

        background-color: $color-gray1;
        border-color: $color-gray4;
        border-style: $border-style-solid;
        border-width: $border-width-default;
        bottom: $measure--quarter;
        color: $color-nearblack;
        font-weight: $font-weight-text;
        height: $measure--medium4;
        position: relative;
        width: 100%;

        &:focus {
            @include outline-properties();
        }
    }

    &.finder__select--selected {
        select {
            background-color: $color-red60;
            border-color: $color-red60;
            color: $color-purewhite;
            font-weight: $font-weight-medium;
        }
    }
}

// Form rendering is slightly different across browsers. This helps align select menu with overline in Firefox.
@-moz-document url-prefix() {
    .finder__select select {
        margin-left: -$measure--quarter;
    }
}

.finder__select__overline {
    @include smalltype();

    color: $color-red50;
    display: block;
    font-weight: $font-weight-text;
    padding-bottom: $measure--basic;
}

.finder__input-text__wrapper {
    background-color: $color-purewhite;
    display: flex;
    flex: 1;
    padding: $measure--half $measure--threequarters $measure--quarter
        $measure--quarter;
}

.finder__input-text__label {
    display: none;
}

.finder__icon {
    background-color: $color-purewhite;
    color: $color-gray7;
    font-weight: $font-weight-text;
    margin-bottom: $measure--half;
    margin-top: $measure--threequarters;
}

.finder__icon--close {
    background-color: $color-gray1;
    color: $color-gray10;
}

.finder__icon--close__wrapper {
    font-weight: $font-weight-text;
}

.edge {
    .finder__query__input {
        input {
            &:focus {
                @include outline-properties();
            }
        }

        .finder__clear {
            &:focus {
                @include outline-properties();
            }
        }
    }

    .finder__select select {
        &:focus {
            @include outline-properties();
        }
    }
}

.finder__results__course {
    .clearing_label {
        margin-bottom: $measure--basic;

        @include media('>=tablet') {
            min-width: 24%;
            order: 1;
        }

        p {
            background-color: $color-yellow40;
            font-weight: bold;
            margin-top: 0;
            padding: $measure--threequarters;

            @include media('<tablet') {
                display: inline;
            }

            span {
                margin-left: $measure--half;
            }
        }
    }
}
