////
/// Quotation
/// @group patterns
////
.blockquote__body {
    padding-left: $measure--small1;
    padding-right: $measure--small1;
    padding-bottom: $measure--half;
    color: $color-gray11;
}

.blockquote__body p {
    @include standfirst();

    em {
        font-style: normal;
    }
}

.blockquote__body--text-align-left {
    text-align: left;
}

.blockquote__body--text-align-right {
    text-align: right;
}

.blockquote__body--text-align-center {
    text-align: center;
}

.blockquote__body svg {
    margin-top: $measure--small3;
    margin-bottom: $measure--small3;
}

.blockquote__body .svg--red {
    fill: $color-red50;
}

.blockquote__body .svg--black {
    fill: $color-nearblack;
}

blockquote footer {
    padding-left: $measure--small1;
    margin-bottom: $measure--small3;
    text-align: left;
    font-size: $font-size--alpha;
    color: $color-gray7;

    cite {
        font-style: normal;
    }
}
