////
/// Menu
/// @group patterns
////
.menu {
    @include media('<small') {
        display: block;

        .menu__level1,
        .menu__level2,
        .menu__level3,
        .menu__level4,
        .menu__level5,
        .menu__level6 {
            list-style: none;
            margin-left: 0;
            margin-top: 0;
            max-width: unset;

            & > li > .menu__controls {
                & > button {
                    align-self: center;
                    margin-left: $measure--threequarters;
                    margin-right: $measure--threequarters;
                    padding-bottom: $measure--quarter;
                    padding-left: $measure--quarter;
                    padding-right: $measure--quarter;
                    padding-top: $measure--quarter;

                    .menu__button__icon {
                        &:before {
                            @include font-family('icon');
                            content: '\f067';
                            font-weight: $font-weight-light;
                        }
                    }

                    .menu__button__text {
                        @include screenreader-only();
                    }

                    &[aria-expanded='true'] {
                        .menu__button__icon {
                            &:before {
                                content: '\f068';
                            }
                        }
                    }
                }
            }

            li {
                &.menu__current > .menu__controls > span > span {
                    display: inline;
                    border-bottom: $border-width-default $border-style-solid
                        $color-red50;
                }
            }
        }

        .menu__level1,
        .menu__level2 {
            li {
                @include content-style();
                @include font-family('heading');
                @include heading-font-features();

                font-weight: $font-weight-normal;
                max-width: unset;

                &.menu__hierarchy > .menu__controls > a {
                    span {
                        border-bottom: $border-width-default $border-style-solid
                            $color-gray2;
                    }
                }

                &.menu__current > .menu__controls > span:focus {
                    outline: none;

                    > span {
                        @include outline-properties($color-purewhite);
                    }
                }
            }
        }

        .menu__level3,
        .menu__level4,
        .menu__level5,
        .menu__level6 {
            li {
                @include content-style();
                @include smalltype();

                font-weight: $font-weight-text;
                max-width: unset;

                &.menu__hierarchy > .menu__controls > a {
                    span {
                        border-bottom: $border-width-default $border-style-solid
                            $color-gray9;
                    }
                }

                &.menu__current > .menu__controls > span:focus {
                    outline: none;

                    > span {
                        @include outline-properties($color-nearblack);
                    }
                }
            }
        }

        .menu__level1 {
            background-color: $color-gray10;
            color: $color-purewhite;

            > li {
                @include betatype();
                line-height: $line-height-ratio--body;
            }

            & > li {
                > .menu__controls {
                    border-bottom: $border-width-hairline $border-style-solid
                        $color-gray8;

                    & > a {
                        @include navigation-anchor(
                            $color: $color-purewhite,
                            $background: $color-gray10,
                            $hover-color: $color-purewhite,
                            $hover-background: $color-gray8,
                            $outline: $color-purewhite
                        );
                    }

                    & > button {
                        @include navigation-button(
                            $color-purewhite,
                            $color-gray10,
                            $color-gray8,
                            $color-purewhite
                        );
                    }

                    & > a,
                    & > span {
                        padding-left: $measure--half;
                    }
                }

                &:last-of-type[data-open='false'] {
                    > .menu__controls {
                        border-bottom: none;
                    }
                }
            }
        }

        .menu__level2 {
            background-color: $color-gray9;
            color: $color-purewhite;

            > li {
                @include alphatype();
                line-height: $line-height-ratio--body;
            }

            & > li {
                > .menu__controls {
                    border-bottom: $border-width-hairline $border-style-solid
                        $color-gray7;

                    & > a {
                        @include navigation-anchor(
                            $color: $color-purewhite,
                            $background: $color-gray9,
                            $hover-color: $color-purewhite,
                            $hover-background: $color-gray7,
                            $outline: $color-purewhite
                        );
                    }

                    & > button {
                        @include navigation-button(
                            $color-purewhite,
                            $color-gray9,
                            $color-gray7,
                            $color-purewhite
                        );
                    }

                    & > a,
                    & > span {
                        padding-left: #{2 * $measure--half};
                    }
                }

                &:last-of-type[data-open='false'] {
                    > .menu__controls {
                        border-bottom: none;
                    }
                }
            }
        }

        .menu__level3 {
            background-color: $color-gray1;
            color: $color-nearblack;

            & > li > .menu__controls {
                & > a {
                    @include navigation-anchor(
                        $color: $color-nearblack,
                        $background: $color-gray1,
                        $hover-color: $color-nearblack,
                        $hover-background: $color-gray3,
                        $outline: $color-nearblack
                    );
                }

                & > button {
                    @include navigation-button(
                        $color-nearblack,
                        $color-gray1,
                        $color-gray3,
                        $color-nearblack
                    );
                }

                & > a,
                & > span {
                    padding-left: #{3 * $measure--half};
                }
            }
        }

        .menu__level4 {
            background-color: $color-gray2;
            color: $color-nearblack;

            & > li > .menu__controls {
                & > a {
                    @include navigation-anchor(
                        $color: $color-nearblack,
                        $background: $color-gray2,
                        $hover-color: $color-nearblack,
                        $hover-background: $color-gray4,
                        $outline: $color-nearblack
                    );
                }

                & > button {
                    @include navigation-button(
                        $color-nearblack,
                        $color-gray2,
                        $color-gray4,
                        $color-nearblack
                    );
                }

                & > a,
                & > span {
                    padding-left: #{4 * $measure--half};
                }
            }
        }

        .menu__level5 {
            background-color: $color-gray3;
            color: $color-nearblack;

            & > li > .menu__controls {
                & > a {
                    @include navigation-anchor(
                        $color: $color-nearblack,
                        $background: $color-gray3,
                        $hover-color: $color-nearblack,
                        $hover-background: $color-gray5,
                        $outline: $color-nearblack
                    );
                }

                & > button {
                    @include navigation-button(
                        $color-nearblack,
                        $color-gray3,
                        $color-gray5,
                        $color-nearblack
                    );
                }

                & > a,
                & > span {
                    padding-left: #{5 * $measure--half};
                }
            }
        }

        .menu__level6 {
            background-color: $color-gray4;
            color: $color-nearblack;

            & > li > .menu__controls {
                & > a {
                    @include navigation-anchor(
                        $color: $color-nearblack,
                        $background: $color-gray4,
                        $hover-color: $color-nearblack,
                        $hover-background: $color-gray6,
                        $outline: $color-nearblack
                    );
                }

                & > button {
                    @include navigation-button(
                        $color-nearblack,
                        $color-gray4,
                        $color-gray6,
                        $color-nearblack
                    );
                }

                & > a,
                & > span {
                    padding-left: #{6 * $measure--half};
                }
            }
        }

        .menu__controls {
            display: flex;
            flex-direction: row;
            flex-wrap: nowrap;
            justify-content: space-between;
        }

        a,
        .menu__current > .menu__controls > span {
            display: block;
            padding: $measure--half;
            width: 100%;
        }

        li {
            margin: 0;
        }
    }

    @include media('print') {
        display: none;
    }
}

.no-js .menu {
    display: none;
}

.js {
    .menu {
        @include media('>=small') {
            display: none;
        }

        align-self: stretch;
        z-index: z-index('navigation');

        .menu__content {
            display: none;

            .menu__level1 {
                ul {
                    display: none;
                }
            }

            .menu__utility {
                background-color: $color-gray13;
                border-top: $border-width-thicker $border-style-solid
                    $color-red50;
                color: $color-nearblack;
                padding-bottom: $measure--small2;
                padding-top: $measure--small2;

                ul {
                    align-items: center;
                    display: flex;
                    flex-direction: row;
                    flex-wrap: wrap;
                    height: 100%;
                    justify-content: space-around;
                    list-style: none;
                    margin-left: 0;
                    margin-top: 0;
                    max-width: unset;

                    li {
                        @include h6style();

                        font-weight: $font-weight-medium;
                        margin-top: 0;

                        a {
                            @include underline-transition--subelement(
                                $color-gray8,
                                $color-nearblack,
                                $border-width-thick,
                                $color-nearblack,
                                'global-header__utility__text'
                            );

                            height: 100%;

                            .global-header__utility__icon {
                                color: $color-gray8;
                                margin-left: $measure--quarter;
                                top: -#{$border-width-default};
                            }

                            &:hover .global-header__utility__icon {
                                @include media('hover') {
                                    color: $color-nearblack;
                                }
                            }
                        }
                    }
                }
            }
        }

        &[data-open='true'] {
            > .menu__display__button {
                > button {
                    @include cta--basic--subelement(
                        $color-purewhite,
                        $color-gray10,
                        $color-purewhite,
                        $color-gray8,
                        $color-purewhite,
                        square,
                        'menu__button__text'
                    );

                    .menu__button__icon {
                        &:before {
                            content: '\f00d';
                        }
                    }
                }
            }

            > .menu__content {
                box-shadow: 0 $measure--half $measure--small1 0
                    rgba(0, 0, 0, 0.3);
                display: flex;
                flex-direction: column;
                flex-wrap: nowrap;
                left: 0;
                position: absolute;
                right: 0;

                ul {
                    li {
                        &[data-open='true'] {
                            & > .menu__controls > a > span,
                            & > .menu__controls > span > span {
                                font-weight: $font-weight-bold;
                            }

                            > ul {
                                display: block;
                            }
                        }
                    }
                }
            }
        }

        > .menu__display__button {
            height: 100%;

            > button > div {
                min-width: $measure--medium8;
            }

            > button {
                @include h6style();
                @include cta--basic--subelement(
                    $color-purewhite,
                    $color-red50,
                    $color-purewhite,
                    $color-red70,
                    $color-purewhite,
                    square,
                    'menu__button__text'
                );

                @include media('<=mobile') {
                    @include smalltype();
                }

                div {
                    align-items: center;
                    display: flex;
                    flex-direction: row;
                }

                align-items: center;
                flex-direction: row;
                flex-wrap: nowrap;
                height: 100%;
                justify-content: center;
                margin-top: 0;
                min-height: $minimum-control-height;
                width: 100%;

                .menu__button__icon {
                    margin-left: $measure--half;

                    &:before {
                        content: '\f0c9';
                    }
                }
            }
        }

        .menu__content__header {
            @include content-style();
            @include font-family('heading');
            @include heading-font-features();
            @include betatype();

            background-color: $color-gray10;
            border-bottom: $border-width-hairline $border-style-solid
                $color-gray8;
            border-top: $border-width-hairline $border-style-solid $color-gray8;
            color: $color-purewhite;
            font-weight: $font-weight-normal;
            line-height: $line-height-ratio--body;
            max-width: unset;

            a {
                @include navigation-anchor(
                    $color: $color-purewhite,
                    $background: $color-gray10,
                    $hover-color: $color-purewhite,
                    $hover-background: $color-gray8,
                    $outline: $color-purewhite
                );

                > span {
                    border-bottom: $border-width-default $border-style-solid
                        $color-purewhite;
                    display: inline;
                    padding: 0;
                }
            }

            span {
                display: block;
                padding: $measure--half;
                width: 100%;

                > span {
                    border-bottom: $border-width-default $border-style-solid
                        $color-red50;
                    display: inline;
                    padding: 0;
                }

                &:focus {
                    outline: none;

                    > span {
                        @include outline-properties($color-purewhite);
                    }
                }
            }
        }
    }

    .menu__veil {
        @include veil();

        &[data-on='true'] {
            @include media('>=small') {
                display: none;
            }
        }
    }
}
