@import 'promo-content--v2';

.promo-content {
    border: $border-width-hairline $border-style-solid $color-purple70;
    background-color: $color-purple5;
    padding: $measure--threequarters $measure--small4 $measure--small4;
    margin-top: $measure--medium3;

    @include media('>between') {
        padding: $measure--threequarters $measure--medium11 $measure--small4
            $measure--medium11;
    }
}

.promo-content__heading {
    @include h2style;
    color: $color-purple70;
}

.promo-content__short-text {
    color: $color-purple70;
}

.promo-content__long-text {
    @include media('>tablet') {
        margin-top: $measure--threequarters;
    }
}

.promo-content--2col {
    margin-top: $measure--basic;

    @include media('>tablet') {
        display: flex;
    }

    .promo-content__long-text {
        @include media('>tablet') {
            margin-top: 0;
            margin-left: $measure--small5;
        }

        @include media('>between') {
            margin-left: $measure--medium3;
        }
    }
}

.promo-content__graphic {
    margin-top: $measure--threequarters;

    span {
        color: $color-purple70;
    }

    span.fad {
        font-size: $measure--medium3;
    }

    @include media('>tablet', '<desktop') {
        max-width: 13rem;
    }

    img {
        max-width: 14.44rem;

        @include media('>tablet', '<desktop') {
            max-width: 100%;
        }
    }
}

.promo-content--icon-graphic {
    .promo-content__graphic {
        border-top: $border-width-thicker $border-style-solid $color-purple70;
        padding-top: $measure--small2;
    }

    .promo-content__graphic__icon {
        width: $measure--medium4;
    }
}

.promo-content__graphic--stat {
    @include font-family('heading');
    @include thetatype;
    font-weight: $font-weight-light;
}
