.card--contact {
    background: $color-purewhite;
    @include box-shadow();
    padding: $measure--small1;

    &:last-child {
        @include media('<tablet') {
            margin-top: $measure--basic;
        }
    }
}

.card--contact__name,
.card--contact__position,
.card--contact__key-info {
}

.card--contact__name {
    margin-top: $measure--small2;
    font-weight: $font-weight-medium;
}

.card--contact__key-info {
    display: flex;
    word-break: break-word;
    margin-top: $measure--small2;

    span {
        @include alphatype();
    }

    p {
        margin-top: 0;
    }

    .icon {
        color: $color-red50;
        margin-right: $measure--half;
        @include alphatype();
    }
}
