// Related links with images
.related-links--with-images__wrapper {
    padding: $measure--basic;

    &.background--gray2,
    &.background--purewhite {
        span:before {
            color: $color-inline-link;
            font-size: $font-size--alpha;
        }

        a {
            @include anchor-inline($color-inline-link, $color-inline-link);
        }
    }

    &.background--purewhite {
        background-color: $color-purewhite;
    }

    &.background--pink80 {
        background-color: $color-pink80;
    }

    &.background--red60 {
        background-color: $color-red60;
    }

    &.background--pink80,
    &.background--red60 {
        h2 {
            color: $color-purewhite;
        }

        span:before {
            color: $color-gray4;
            font-size: $font-size--alpha;
        }

        a {
            @include anchor-inline($color-gray4, $color-gray4);
        }
    }

    .related-links--with-images__2-columns {
        display: flex;
        flex-wrap: wrap;
    }

    .related-links--with-images {
        padding: $measure--basic;
        flex-basis: 50%;

        @include media('<tablet') {
            flex-basis: 100%;
        }

        h2 {
            text-align: left;
        }

        img {
            width: 100%;
        }

        a {
            margin-left: $measure--half;
            font-weight: $font-weight-normal;

            &:after {
                display: none;
                content: none;
            }
        }

        .pathway-image-cards__blocks {
            display: block;
            margin-top: 0;

            p {
                span {
                    display: inline;
                }

                a {
                    display: inline;
                }
            }
        }
    }

    .pathway-image-cards {
        background: inherit;
    }
}
