.card--course {
    list-style: none;
    margin-bottom: $measure--small4;
    padding: $measure--basic;
    position: relative;

    @include background-color-and-scale;

    p {
        margin: 0 0 $measure--basic 0;
        @include alphatype();
        color: $color-gray10;
    }
}

.card--course__type {
    color: $color-red50;
    @include smalltype();
    margin-top: $measure--basic;
}

.card--course__key-info {
    @include smalltype();
    color: $color-gray7;
    margin-top: $measure--quarter;

    .icon {
        color: $color-red50;
        margin-right: $measure--half;
    }
}
