.module-selector-wrapper {
    padding: $measure--medium1 0;
}

.module-selector {
    @include standard-box();
    @include gradient-border($color-red50, $color-pink60);
    position: relative;
    padding: $measure--basic;
    margin-bottom: $measure--small4;
    background-color: $color-red5;
    overflow: hidden;
    text-align: center;

    @include media('>=between') {
        @include standard-grid;
        text-align: left;
        padding: $measure--small4;
    }
}

.module-selector__heading {
    @include h3style;
}

.module-selector__heading {
    grid-column: 1 / 6;
}

.module-selector__content {
    grid-column: 7 / 13;
    .module-selector__content__text {
        max-width: 100%;
    }
}

.module-selector__button {
    margin: $measure--small4 0 0 0;
    grid-column: 14 / 18;

    @include media('>=between') {
        margin: 0;
    }
    @include media('<tablet') {
        display: flex;
        flex-direction: column;

        a {
            display: block;
        }
    }

    a {
        @include alphatype();
    }
}

.module-selector__content,
.module-selector__heading {
    margin-bottom: $measure--basic;

    @include media('>between') {
        margin-bottom: 0;
    }
}

.module-selector__content,
.module-selector__heading,
.module-selector__button {
    align-self: center;
    position: relative;
}

.module-selector__heading-text {
    color: $color-gray7;
}

.module-selector__background {
    display: block;
    right: 0;
    position: absolute;
    right: -$measure--large2;
    height: 100%;
    top: 0;

    @include media('>=tablet') {
        right: 0;
    }
}

.no-js {
    .module-selector__button {
        margin: $measure--basic 0 0 0;
    }

    .module-selector {
        @include media('>=tablet') {
            display: block;
        }

        .modal__content {
            background: transparent;
        }
    }

    .module-selector__button {
        text-align: left;
    }
}
