////
/// Responsive Slider
/// Dynamic slide display, e.g. multiple cards per slide on tablets and desktop
/// @group patterns
////

@mixin slider-responsive() {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;

    // Slides
    .slider__slide {
        margin-top: $measure--small3;
        @include media('>mobile') {
            flex-basis: 100%;
        }
        @include media('>tablet') {
            flex-basis: 50%;
        }
        @include media('>desktop') {
            flex-basis: 33.33%;
        }
        &[data-device='mobile'] {
            display: block;
        }
        &[data-device='tablet'] {
            @include media('>tablet') {
                display: block;
            }
        }
        &[data-device='desktop'] {
            @include media('>desktop') {
                display: block;
            }
        }
    }

    // Controls
    .slider__controls__wrap {
        display: none;
        margin: $measure--basic 0 $measure--small4 0;
        flex-basis: 100%;
        max-width: none;
        &[data-device='mobile'] {
            @include media('<tablet') {
                display: flex;
            }
        }
        &[data-device='tablet'] {
            @include media('>=tablet', '<desktop') {
                display: flex;
            }
        }
        &[data-device='desktop'] {
            @include media('>=desktop') {
                display: flex;
            }
        }
        &[data-hide='true'] {
            display: none;
        }
    }

    .slider__controls__progress {
        color: $color-gray7;
        margin: $measure--basic $measure--small4 0 0;
        font-size: $font-size--extrasmalltype;
        font-weight: $font-weight-book;
    }

    .slide__controls__progress__active {
        color: $color-red50;
    }

    .slider__controls__buttons__next,
    .slider__controls__buttons__prev {
        border: $border-width-hairline $border-style-solid $color-red50;
        padding: $measure--threequarters;
        background: $color-purewhite;
        margin-left: $measure--basic;
        @include anchor-default-outline($color-outline);
        &:hover {
            @include media('hover') {
                background: $color-red50;
                color: $color-purewhite;
            }
        }
        &:focus,
        &:active {
            color: $color-purewhite;
            background: $color-red50;
        }
        &:before {
            font-weight: $font-weight-normal;
        }
        &[disabled='true'] {
            color: $color-gray7;
            background: $color-purewhite;
            border: none;
            opacity: 0.7;
            cursor: auto;
        }
    }
}

.slider-responsive {
    @include slider-responsive();
}
