.card--profile {
    border-top: $border-width-hairline $border-style-dotted $color-gray4;
    padding: $measure--small4 0;

    &:first-child {
        border-top: none;
        padding-top: 0;
    }
}

.card--profile__name {
    color: $color-gray10;
}

.card--profile__jobtitle {
    @include smalltype();
    color: $color-gray7;
}

.card--profile__description {
    margin-top: $measure--half;
}

.card--profile__description--thumbnail {
    @include card-with-thumbnail;
}

.card--profile__thumbnail {
    border-radius: 100%;
    max-width: $measure--large4;
    max-height: $measure--large2;
    margin-top: $measure--half;

    @include media('>=tablet') {
        margin: $measure--half $measure--basic $measure--basic 0;
    }
}
