main {
    // Desktop-specific layout
    .landing {
        header {
            p {
                @include standfirst();
            }
        }

        @include media('>=desktop') {
            grid-column: 6 / span 14;
        }

        @include media('>=small') {
            .landing--functional__content__wrap {
                @include content-grid();

                .landing--functional__content {
                    grid-column: 4 / span 12;
                }
            }
        }
    }

    // Tablet mobile layout
    @include media('>mobile', '<small') {
        .wrapper--finder__mini {
            @include standard-grid;

            .finder__mini {
                grid-column: 2 / 24;
            }

            .landing--functional__finder__cta__wrap {
                grid-column: 2 / 24;
            }
        }

        .landing--functional__content__wrap {
            @include standard-grid;

            .landing--functional__content {
                grid-column: 7 / span 12;
            }
        }
    }

    // Desktop specific layout if secondary navigation is present
    .navigation--secondary {
        & + .landing {
            @include media('>=small') {
                grid-column: 7 / span 18;

                .wrapper--finder__mini {
                    @include standard-grid;
                    @include x-column-grid(18);

                    .finder__mini {
                        grid-column: 3 / 17;
                    }

                    .landing--functional__finder__cta__wrap {
                        grid-column: 3 / 17;
                    }
                }

                .landing--functional__content__wrap {
                    @include content-grid();

                    .landing--functional__content {
                        grid-column: 4 / span 12;
                    }
                }
            }

            .landing__top__content {
                @include content-grid();
                padding: $measure--small4;

                .landing__top__content--text,
                .landing__top__content--heading {
                    grid-column: 4/ -4;
                }
            }

            .landing_middle1_content {
                @include content-grid();
                padding: $measure--small4;

                .landing__middle1__content--heading,
                .landing__middle1__content--text {
                    grid-column: 4/ -4;
                }
            }

            .landing__middle1 {
                & > h2 {
                    padding-left: $measure--small4;
                    padding-right: $measure--small4;
                    padding-top: $measure--small4;
                }

                & > p {
                    padding-left: $measure--small4;
                    padding-right: $measure--small4;
                }

                .landing__middle1__pathway-image-cards {
                    h2 {
                        padding: 0;
                    }
                }
            }
        }
    }

    .landing--functional__finder__cta {
        margin-bottom: $measure--threequarters;
        a {
            //@include anchor-inline();

            &:after {
                content: '\f061';
                font-family: 'Font Awesome';
            }
        }
    }

    .landing:only-child {
        grid-column: 1/-1;

        .landing__top__content {
            padding: $measure--basic 0 $measure--small4 0;
            @include standard-grid();

            .landing__top__content--heading {
                grid-column: 8/18;
                @include media('<=between') {
                    grid-column: 2/-2;
                }
            }
            .landing__top__content--text {
                grid-column: 8/18;
                @include media('<=between') {
                    grid-column: 2/-2;
                }
            }
        }

        .landing_middle1_content {
            @include standard-grid();

            margin: $measure--basic 0;

            .landing__middle1__content--heading {
                grid-column: 8/18;
                @include media('<=between') {
                    grid-column: 2/-2;
                }
            }
            .landing__middle1__content--text {
                grid-column: 8/18;
                @include media('<=between') {
                    grid-column: 2/-2;
                }
            }
        }

        .landing__middle1 {
            @include standard-grid();

            > div {
                grid-column: 1/-1;
            }

            .accordion {
                grid-column: 8/18;
                padding-top: $measure--small4;
                padding-bottom: $measure--small4;

                @include media('<between') {
                    grid-column: 1/-1;
                }
            }

            h2 {
                grid-column: 8/18;
                @include media('<=between') {
                    grid-column: 2/-2;
                }
            }
            p {
                grid-column: 8/18;
                @include media('<=between') {
                    grid-column: 2/-2;
                }
            }
        }

        .banner {
            grid-template-areas: initial;

            .banner__image {
                grid-column: 1/-1;
            }

            .banner__standfirst {
                @include media('>tablet') {
                    grid-column: 2/-2;
                }
                @include media('>between') {
                    grid-column: 4/22;
                }
            }
        }

        .swiper-viewport {
            @include media('>between') {
                grid-template-areas: '. . . . . . . slider-container slider-container slider-container slider-container slider-container slider-container slider-container slider-container slider-container slider-container slider-container slider-container slider-container slider-container slider-container slider-container slider-container slider-container slider-container';
            }
            @include media('<=between') {
                grid-template-areas: intial;

                .swiper-container {
                    grid-column: 1/-1;
                }

                .swiper-buttons-wrap {
                    right: $measure--basic;
                    bottom: $measure--half;
                }
                .swiper-indicator {
                    right: $measure--huge3;
                    bottom: $measure--basic;
                }
            }
        }

        .featured-profiles-block {
            max-width: initial;
        }

        .landing__top__cta {
            padding: $measure--medium3 0;
        }
    }
}

@include media('<small') {
    .landing--functional__finder {
        .landing--functional__finder__tabs {
            @include default-margin();
        }
    }
}

.landing--functional__finder__tabs {
    margin-top: $measure--small4;

    .landing--functional__finder__tabs__heading {
        text-align: center;
        position: relative;

        &:after {
            content: '';
            border-top: $border-width-default $border-style-solid $color-gray2;
            width: 100%;
            display: block;
            top: $measure--basic;
            position: absolute;
            z-index: z-index('below');
        }

        h2 {
            background: $color-purewhite;
            display: inline;

            padding-left: $measure--small4;
            padding-right: $measure--small4;
        }
    }
}

@include media('<mobile') {
    .landing--functional__content {
        //@include default-padding();
        @include standard-box();
    }

    .wrapper--finder__mini {
        @include default-margin();
    }
}
.landing--functional {
    .finder__query {
        margin: 0;
    }
}

.landing__top {
    margin-bottom: $measure--basic;

    > h2 {
        text-align: center;
    }

    .carousel-gallery-default {
        background-color: initial;
        @include standard-grid();

        .carousel-gallery {
            background-color: #3e1031;
            padding: $measure--basic;
            grid-column: 4/15;
        }
    }

    .figure--image {
        @include standard-grid();

        picture,
        figcaption {
            grid-column: 1/-1;
            @include media('<=between') {
                grid-column: 2/-2;
            }
        }
    }
}

.landing__top__cta {
    display: flex;
    justify-content: center;
}

.cta-block {
    display: flex;
    .cta-block__cta {
        margin-right: $measure--basic;
    }
}

.landing__middle1__pathway-image-cards {
    padding: $measure--small4;
}

.landing__middle2 {
    > h2 {
        text-align: center;
    }

    .swiper-viewport {
        padding-top: $measure--small4;
        padding-bottom: $measure--small4;
    }

    .slider__active-slide {
        @include media('>=between') {
            grid-column: 6/20;
        }
        @include media('<between') {
            grid-column: 2/-2;
        }
    }

    .slider__controls__wrap {
        @include media('>=between') {
            grid-column: 6/20;
        }
        @include media('<between') {
            grid-column: 2/-2;
        }
    }
}

.landing__whats-on {
    .related-content {
        @include media('>between') {
            @include standard-grid();
        }

        li:nth-of-type(1) {
            grid-column: 1 / span 8;

            @include media('>tablet', '<=between') {
                grid-column: 1/5;
            }
        }
        li:nth-of-type(2) {
            grid-column: 9 / span 8;
            @include media('>tablet', '<=between') {
                grid-column: 5/9;
            }
        }
        li:nth-of-type(3) {
            grid-column: 17/-1;
            @include media('>tablet', '<=between') {
                grid-column: 9/-1;
            }
        }
    }
}

.landing__bottom {
    @include media('>between') {
        h2 {
            text-align: center;
        }
    }
    @include media('>between') {
        .landing__bottom__related-content {
            .related-content {
                display: flex;
                flex-wrap: wrap;

                .card {
                    width: calc(1 / 2 * 100%);

                    a {
                        width: 100%;
                        height: 100%;
                    }
                }
            }
        }
    }
}

.landing__contact {
    background-color: $color-pink80;
    padding: $measure--small4;
    margin-top: $measure--basic;

    @include media('>between') {
        @include standard-grid();
    }

    .landing__contact__sec {
        grid-column: 1 / span 16;
        color: $color-purewhite;

        .related-content {
            .card--contact__key-info__wrap {
                margin-top: $measure--small4;
            }

            .card--contact__key-info {
                margin-top: $measure--threequarters;

                a {
                    @include anchor-inline(
                        $color-gray4,
                        $color-gray4,
                        $color-gray11,
                        $color-outline--dark
                    );
                }
            }

            @include media('>between') {
                @include sixteen-grid();
            }

            li:nth-child(1) {
                grid-column: 1 / span 8;
                @include media('<=between') {
                    border-bottom: $border-width-default solid $color-pink70;
                    padding-bottom: $measure--small4;
                }
            }

            li:nth-child(2) {
                grid-column: 9 / span 8;
            }
        }
    }

    .landing__contact__3rdcol {
        grid-column: 17 / span 8;

        @include media('<between') {
            margin-top: $measure--small4;
        }

        .unibuddy-card {
            background-color: $color-purewhite;
        }

        .landing__contact__sec--heading {
            color: $color-purewhite;
        }

        .card--findus {
            a {
                box-shadow: none;
                &:focus {
                    outline-color: $color-purewhite;
                }
            }

            .card--findus__content {
                padding-top: 0;

                @include media('<between') {
                    padding: 0;
                }
            }

            .card--findus__content__header {
                display: none;
            }

            .card--findus__content__name {
                @include anchor-inline(
                    $color-gray4,
                    $color-gray4,
                    $color-gray11,
                    $color-outline--dark
                );
            }

            .card--findus__content__address {
                color: $color-purewhite;

                .icon {
                    color: $color-purewhite;
                }
            }
        }
    }

    .card--contact {
        background: initial;
        box-shadow: initial;
        padding: 0;

        .card--contact__key-info > .icon {
            color: $color-purewhite;
        }
    }
}

.landing__contact__sec--heading {
    border-bottom: $border-width-default solid $color-pink70;
    padding-bottom: $measure--half;
    h2 {
        text-align: left;
    }
}
