////
/// Footer - This is for the reusable footer element on course pages paint layouts asset id 503750
/// @group patterns
////

.course-footer {
    @include standard-box();
    max-width: 80rem;
    padding-top: $measure--small2;
    padding-bottom: $measure--medium3;

    h2 {
        margin-bottom: $measure--small2;
    }

    .course-footer__columns-two,
    .course-footer__columns-three {
        display: flex;

        @include media('<between') {
            flex-direction: column;
        }
        .course-footer__contact {
            display: flex;
            flex-direction: column;
            flex-basis: 66%;
        }

        .related-content {
            display: flex;
            margin-right: $measure--small2;
            padding-top: $measure--small2;
            border-top: $border-width-hairline $border-style-solid $color-gray4;

            @include media('<between') {
                flex-basis: 100%;
                margin-right: 0;
            }

            @include media('<tablet') {
                display: inline-block;
            }

            li {
                flex: 1;
                &:not(:last-child) {
                    margin-right: $measure--small2;

                    @include media('<tablet') {
                        margin-right: 0;
                    }
                }
            }
        }
    }

    .course-footer__columns-two {
        @include media('>tablet') {
            flex-direction: row;

            .related-content {
                margin-right: $measure--small2;
            }

            .useful-links {
                margin-top: 0;
            }
        }

        .course-footer__contact {
            flex-basis: 33%;

            @include media('<between') {
                flex-basis: 50%;
            }

            @include media('<tablet') {
                flex-basis: 100%;
            }
        }
    }

    .useful-links {
        flex-basis: 33%;

        @include media('<between') {
            margin-top: $measure--basic;
        }
    }
}
