.banner-text {
    background-color: $color-gray11;
    color: $color-purewhite;
    padding-top: $measure--medium9;
    padding-bottom: $measure--medium3;

    .banner-text__content {
        @include default-margin();
        @include default-padding();
    }

    // Centered banners, as used on Research degrees for example
    &.banner--center {
        .banner-text__content {
            @include standard-grid();
            @include standard-box();
            h1,
            p {
                grid-column: 1/-1;
                @include media('>tablet') {
                    grid-column: 2/-2;
                }
                @include media('>between') {
                    grid-column: 3/-3;
                }
                @include media('>desktop') {
                    grid-column: 5/15;
                }
            }
        }
    }
}
