.banner__standfirst {
    background: $color-blue90;
    color: $color-purewhite;
    opacity: 0.9;
    padding: $measure--small4;
    position: relative;
    bottom: 0;

    @include media('<desktop') {
        grid-column: 1 / -1;
    }

    @include media('>=desktop') {
        grid-column: 4 / 16;
        bottom: $measure--large2;
    }
}
