$chart-themes: (
    'purple': (
        $color-purple40,
        $color-purple60,
        $color-purple80,
    ),
    'red': (
        $color-red40,
        $color-red60,
        $color-red80,
    ),
    'pink': (
        $color-pink40,
        $color-pink60,
        $color-pink80,
    ),
);

@each $color-key, $color-values in $chart-themes {
    @each $color in $color-values {
        .chart--#{$color-key} {
            $i: index($color-values, $color);

            path:nth-child(#{$i}) {
                stroke: $color;
            }

            .chart__legend__item:nth-child(#{$i}) {
                .fa.fa-circle {
                    color: $color;
                }
            }
        }
    }
}

.chart-row {
    margin-top: $measure--medium7;
    @include standard-box;

    @include media('>=tablet') {
        display: flex;
    }
}

.chart {
    margin-bottom: $measure--small4;
    margin-right: $measure--small4;

    &:last-child {
        margin-right: 0;
    }

    @include media('>=tablet') {
        flex: 0 0 20%;
    }
}

.chart--piechart {
    margin-right: $measure--medium4;
    svg {
        width: 100%;
    }
}

.chart__legend {
    justify-content: center;
    display: flex;
    flex-direction: column;
}

.chart__legend__item__icon {
    font-size: $measure--small1;
    margin-right: $measure--threequarters;
}

.chart__legend {
    margin-top: $measure--small2;
}

.chart__title {
    margin-bottom: $measure--small2;
}

.chart__legend__item {
    margin-bottom: $measure--basic;
    align-items: center;
    display: flex;
    line-height: 1.5;
}

.chart--piechart__value {
    @include svg-path-animation;
}

.chart--bar--horizontal__collection {
    display: flex;
    width: 100%;
    margin-bottom: $measure--basic;
    position: relative;
    align-items: center;
    padding: $measure--small2 $measure--half;
    color: $color-purewhite;
}

.chart--bar--horizontal__category {
    position: absolute;
    right: $measure--basic;
}

.chart--bar--horizontal__value {
    padding: $measure--basic $measure--half;
    position: absolute;
    top: 0;
    left: 0;
    align-items: center;
    height: 100%;
    display: flex;
    min-width: 30%;

    &:after {
        content: '';
        position: absolute;
        right: -$measure--half;
        width: $measure--half;
        height: 0px;
        border-top: $measure--half $border-style-solid transparent;
        border-bottom: $measure--half $border-style-solid transparent;
        border-left: $measure--half $border-style-solid $color-pink40;
    }

    span {
        &[data-hidden='true'] {
            @include media('<large') {
                display: none;
            }
        }
    }
}

.chart--bar--horizontal.chart--pink {
    .chart--bar--horizontal__value {
        background: $color-pink40;
    }

    .chart--bar--horizontal__collection {
        background: $color-pink80;
    }
}
