.no-js {
    .how-to-apply--pgt--no-js {
        display: block;
    }

    .tabs .how-to-apply--pgt--js {
        display: none;
    }
}

.js {
    .how-to-apply--pgt--no-js {
        display: none;
    }

    .tabs .how-to-apply--pgt--js {
        display: block;
    }

    .how-to-apply--pgt--js__modal {
        display: none;

        &[data-open='true'] {
            display: block;
        }
    }
}

.how-to-apply--pgt--js__modal {
    @include veil();

    bottom: 0;
    left: 0;
    position: fixed;
    right: 0;
    top: 0;
    z-index: z-index('modal');

    .how-to-apply--pgt--js__modal__content {
        background-color: $color-purewhite;
        box-shadow: 0 $measure--half $measure--small1 0 rgba(0, 0, 0, 0.3);
        color: $color-nearblack;
        display: block;
        margin-left: auto;
        margin-right: auto;
        margin-top: $measure--small2;
        max-height: calc(100% - #{2 * $measure--small1});
        overflow-y: scroll;
        padding-bottom: $measure--small3;
        padding-left: $measure--small2;
        padding-right: $measure--small2;
        padding-top: $measure--small3;
        position: relative;
        text-align: left;
        width: 90%;

        @include media('>=tablet') {
            margin-top: 15vh;
            max-height: calc(100% - 30vh);
        }

        @include media('>=small') {
            width: 70%;
        }

        @include media('>=desktop') {
            width: 60%;
        }

        .how-to-apply--pgt--js__modal__content-wrapper {
            margin-left: auto;
            margin-right: auto;
            max-width: $max-line-length;
        }

        p {
            @include content-style();

            text-align: left;
        }

        .how-to-apply--pgt--js__options {
            display: flex;
            flex-direction: row;
            flex-wrap: wrap;
            justify-content: center;
            list-style: none;
            margin-left: 0;
            max-width: unset;

            li {
                margin-bottom: $measure--half;
                margin-right: $measure--half;
            }

            button,
            a {
                @include cta--border(
                    $background: $color-purewhite,
                    $color: $color-nearblack,
                    $border: $color-gray4,
                    $hover-color: $color-purewhite,
                    $hover-border: $color-gray4,
                    $outline: $color-nearblack,
                    $corners: square
                );
            }
        }

        .how-to-apply--pgt--js__modal__alternative {
            a {
                @include anchor-inline(
                    $color: $color-inline-link,
                    $visited: $color-inline-link--visited,
                    $hover: $color-text--dark,
                    $outline: $color-outline
                );
            }
        }

        .how-to-apply--pgt--js__close {
            color: $color-gray7;
            padding: $measure--threequarters;
            position: absolute;
            right: 0;
            top: 0;

            @include anchor-default-outline($color-nearblack);

            &:hover {
                @include media('hover') {
                    background-image: none;
                    border: none;
                    background-color: $color-gray1;
                    color: $color-gray10;
                }
            }

            &:active {
                background-color: $color-gray1;
                color: $color-gray10;
            }

            &:focus {
                color: $color-gray10;
            }

            &:active,
            &:focus {
                outline-offset: 0;
            }
        }

        .how-to-apply--pgt--js__modal__reset {
            display: block;
            position: absolute;
            left: 0;
            top: 0;

            button {
                color: $color-red50;
                padding: $measure--threequarters;
                text-align: center;

                @include anchor-default-outline($color-nearblack);

                &:hover {
                    @include media('hover') {
                        background-image: none;
                        border: none;
                        background-color: $color-gray1;
                        color: $color-gray10;
                    }
                }

                &:active {
                    background-color: $color-gray1;
                    color: $color-gray10;
                }

                &:focus {
                    color: $color-gray10;
                }

                &:active,
                &:focus {
                    outline-offset: 0;
                }
            }

            @include media('>=small') {
                display: none;
            }
        }

        .how-to-apply--pgt--js__modal__progress--mobile {
            display: flex;
            flex-direction: row;
            flex-wrap: nowrap;
            justify-content: center;
            list-style: none;
            margin-left: 0;
            margin-top: $measure--small2;
            max-width: unset;
            text-align: left;

            li {
                position: relative;
                text-align: center;
                width: $measure--medium11;

                &:not(:last-of-type) {
                    margin-right: $measure--basic;
                    margin-bottom: $measure--basic;
                }

                .how-to-apply--pgt--js__modal__progress__circle {
                    align-items: center;
                    border-radius: 100%;
                    border-width: $border-width-thick;
                    display: flex;
                    height: $measure--small2;
                    justify-content: center;
                    margin: 0 auto;
                    width: $measure--small2;
                }

                .how-to-apply--pgt--js__modal__progress__text {
                    @include content-style();
                    @include smalltype();

                    color: $color-gray7;
                    display: block;
                    font-weight: $font-weight-text;
                    line-height: $measure--small1;
                    margin-top: $measure--half;
                    text-align: center;
                }

                &.how-to-apply--pgt--js__modal__progress__current {
                    .how-to-apply--pgt--js__modal__progress__circle {
                        background-color: $color-red50;
                        border-color: $color-red50;
                        border-style: $border-style-solid;
                        color: $color-red50;
                    }
                }

                &.how-to-apply--pgt--js__modal__progress__previous {
                    &:after {
                        background-color: $color-red50;
                        content: ' ';
                        display: block;
                        height: $measure--quarter;
                        left: $measure--medium4;
                        position: absolute;
                        top: $measure--half;
                        width: $measure--medium10;
                    }

                    .how-to-apply--pgt--js__modal__progress__circle {
                        background-color: $color-purewhite;
                        border-color: $color-red50;
                        border-style: $border-style-solid;
                        color: $color-red50;
                    }
                }

                &.how-to-apply--pgt--js__modal__progress__next {
                    &:not(.how-to-apply--pgt--js__modal__progress__last) {
                        &:before {
                            background-color: $color-gray3;
                            content: ' ';
                            display: block;
                            height: $measure--quarter;
                            right: $measure--medium4;
                            position: absolute;
                            top: $measure--half;
                            width: $measure--medium9;
                        }
                    }

                    .how-to-apply--pgt--js__modal__progress__circle {
                        border-color: $color-gray3;
                        border-style: $border-style-dashed;

                        .icon {
                            color: $color-purewhite;
                        }
                    }
                }
            }

            @include media('>=small') {
                display: none;
            }
        }

        .how-to-apply--pgt--js__modal__progress {
            display: none;

            @include media('>=small') {
                display: flex;
                flex-direction: row;
                flex-wrap: nowrap;
                justify-content: center;
                list-style: none;
                margin-left: 0;
                margin-top: 0;
                max-width: unset;
                text-align: left;

                li {
                    position: relative;
                    text-align: center;
                    width: $measure--large4;

                    &:not(:last-of-type) {
                        margin-right: $measure--medium3;
                        margin-bottom: $measure--basic;
                    }

                    .how-to-apply--pgt--js__modal__progress__circle {
                        align-items: center;
                        border-radius: 100%;
                        border-width: $border-width-thicker;
                        display: flex;
                        height: $measure--medium1;
                        justify-content: center;
                        margin: 0 auto;
                        width: $measure--medium1;
                    }

                    .how-to-apply--pgt--js__modal__progress__text {
                        @include content-style();
                        @include smalltype();

                        color: $color-gray7;
                        display: block;
                        font-weight: $font-weight-text;
                        line-height: $measure--small1;
                        margin-top: $measure--half;
                        text-align: center;
                    }

                    &.how-to-apply--pgt--js__modal__progress__current {
                        .how-to-apply--pgt--js__modal__progress__circle {
                            background-color: $color-red50;
                            border-color: $color-red50;
                            border-style: $border-style-solid;
                            color: $color-red50;
                        }
                    }

                    &.how-to-apply--pgt--js__modal__progress__previous {
                        @include media('>=small') {
                            &:after {
                                background-color: $color-red50;
                                content: ' ';
                                display: block;
                                height: $measure--quarter;
                                left: $measure--medium9;
                                position: absolute;
                                top: $measure--small1;
                                width: $measure--huge5;
                            }
                        }

                        button {
                            align-items: center;
                            display: flex;
                            flex-direction: column;
                            justify-content: center;

                            &:hover {
                                .how-to-apply--pgt--js__modal__progress__circle {
                                    background-color: $color-red50;
                                    color: $color-purewhite;
                                }

                                .how-to-apply--pgt--js__modal__progress__text {
                                    color: $color-nearblack;
                                }

                                .icon {
                                    &:before {
                                        content: '\f00d';
                                    }
                                }
                            }

                            &:active,
                            &:focus {
                                @include outline-properties($color-nearblack);
                            }
                        }

                        .how-to-apply--pgt--js__modal__progress__circle {
                            background-color: $color-purewhite;
                            border-color: $color-red50;
                            border-style: $border-style-solid;
                            color: $color-red50;
                        }
                    }

                    &.how-to-apply--pgt--js__modal__progress__next {
                        &:not(.how-to-apply--pgt--js__modal__progress__last) {
                            &:before {
                                background-color: $color-gray3;
                                content: ' ';
                                display: block;
                                height: $measure--quarter;
                                right: $measure--medium10;
                                position: absolute;
                                top: $measure--small1;
                                width: $measure--large5;
                            }
                        }

                        .how-to-apply--pgt--js__modal__progress__circle {
                            border-color: $color-gray3;
                            border-style: $border-style-dashed;

                            .icon {
                                color: $color-purewhite;
                            }
                        }
                    }
                }
            }
        }
    }
}
