.card--news {
    background: $color-purewhite;
    @include box-shadow;

    a,
    a:hover,
    a:active,
    a:focus {
        border-bottom: none;
    }

    &:hover {
        @include media('hover') {
            /* 
                Trigger thumbnail transition - note the following
                values should match the mixin call:

                - scale value
                - background opacity

                Mixin: thumbnail-gradient-scale
            */

            .thumbnail__image-container {
                img {
                    transform: scale(1.04);
                }

                &:after {
                    background-color: rgba(0, 0, 0, 0.1);
                }
            }
        }
    }
}

.card--news__image-container {
    @include thumbnail-gradient-scale;

    a:first-child {
        display: block;
    }

    .thumbnail__image-container {
        height: $measure--huge5;
        width: 100%;

        img {
            height: 100%;
            width: 100%;
        }

        @include media('>=tablet') {
            height: 16.66rem;
        }
    }
}

.card--news__button {
    display: inline-flex;
    position: relative;
    top: -$measure--small1;
    left: $measure--small3;
    align-items: center;
    color: $color-purewhite;
    background: $color-red50;
    padding: $measure--half $measure--basic;
    @include smalltype;
    font-weight: $font-weight-medium;

    &:visited {
        color: $color-purewhite;
    }

    &:active,
    &:focus {
        background: $color-red60;
        color: $color-purewhite;
    }

    &:hover {
        @include media('hover') {
            background: $color-red60;

            .card--news__button__icons {
                span:nth-child(1) {
                    animation: slide-right-out 600ms;
                    display: none;
                    visibility: hidden;
                    opacity: 0;
                }

                span:nth-child(2) {
                    display: inherit;
                    visibility: visible;
                    opacity: 1;
                    animation: slide-left-in 600ms;
                }
            }
        }
    }

    .card--news__button__icons {
        text-align: right;
        width: $measure--medium1;
    }

    .card--news__button__icons {
        span {
            margin-left: $measure--basic;
            @include smalltype;
            font-weight: $font-weight-medium;
        }
    }

    .card--news__button__icons {
        span:nth-child(1) {
            animation: slide-right-out 600ms;
            display: none;
            opacity: 0;
            visibility: hidden;
        }
    }
}

.card--news__title-link {
    a {
        @include h5style;
        @include underline-transition;
    }
}

.card--news__content {
    padding: 0 $measure--small3 $measure--small3 $measure--small3;
}

.card--news__date {
    @include smalltype;
    color: $color-gray10;
    margin-top: $measure--small2;
}

.card--news__category {
    color: $color-gray7;
    font-style: italic;
    padding: $measure--half $measure--basic;
    display: inline-block;
}

.card--news--case-study {
    .card--news__title-link {
        margin-top: $measure--small1;
    }
}

.card--news--research {
    .card--news__content {
        padding: $measure--small3;
    }
}
