////
/// Figure
/// @group patterns
////

article figure a {
    @include anchor-img();
}

article figure figcaption {
    @include smalltype();
    color: $color-text--caption;
    font-weight: $font-weight-book;
    margin-top: $measure--basic;
    line-height: $measure--small1;

    a {
        @include anchor-discreet();
    }
}

.figcaption__credit {
    display: block;
}

article figure.figure--svg img {
    @include media('<=mobile') {
        padding-bottom: 100vw;
    }
}
