.form {
    fieldset {
        border: none;
        margin: 0;
        padding: 0;

        .form__label {
            @include content-style();
            display: block;
            margin-top: $measure--basic;
        }

        .form__label--required {
            &:after {
                content: ' *';
                color: $color-required;
            }
        }
    }

    input,
    textarea {
        @include body-baseline();

        &:not([type='submit']) {
            width: 100%;
        }

        &[type='submit'] {
            @include anchor-button();
            margin-top: $measure--basic;
        }
    }

    .form__submit-container {
        text-align: right;
    }

    .form__required-note--marker {
        color: $color-required;
    }
}
