.card--testimonial-feature {
    margin-top: $measure--small4;
    display: grid;
    @include x-column-grid(18);
    position: relative;
}

.card--testimonial-feature .slider__slide {
    display: block;
}

.card--testimonial-feature.slider__active-slide {
    display: grid;
    grid-column: 1 / -1;
}

.card--testimonial-feature__heading__wrap {
    grid-column: 1 / -1;

    h2 {
        margin: $measure--small4;
    }

    @include media('>between') {
        display: none;
    }
}
.card--testimonial-feature__background__wrap {
    grid-column: 2 / 11;

    img {
        width: 100%;
    }

    @include media('<=between') {
        grid-column: 1 / -1;
    }
}

_:-ms-lang(x),
.card--testimonial-feature__details__wrap {
    left: -10%;
}
@media all and (-webkit-min-device-pixel-ratio: 0) and (min-resolution: 0.001dpcm) {
    .card--testimonial-feature__details__wrap {
        left: -30%;
    }
}

.card--testimonial-feature__details__wrap {
    grid-column: 11 / 18;
    position: relative;
    width: 130%;
    display: grid;
    grid-column-gap: 1.5rem;
    grid-template-columns: repeat(9, 1fr);

    @include media('<=between') {
        grid-column: 1 / -1;
        position: relative;
        left: 0;
        width: auto;
        margin-top: -$measure--medium11;
        @include standard-grid();
        margin-left: $measure--small4;
        margin-right: $measure--small4;
    }

    @include media('<mobile') {
        margin-left: $measure--small2;
        margin-right: $measure--small2;
    }
}

.card--testimonial-feature__details__title {
    margin-top: $measure--small4;
    margin-bottom: $measure--basic;
    grid-column: 3 / -1;

    h2 {
        margin: 0;
    }

    @include media('<=between') {
        display: none;
    }
}

.card--testimonial-feature__details {
    background: $color-purewhite;
    padding-bottom: $measure--small4;
    margin-bottom: $measure--small4;
    grid-column: 1 / -1;
    @include box-shadow();

    &:before {
        content: '';
        background: $color-red50;
        height: $measure--threequarters;
        display: block;
        margin-left: $measure--medium1;
        margin-right: $measure--medium1;
        margin-bottom: $measure--small4;

        @include media('<desktop') {
            margin-left: $measure--basic;
            margin-right: $measure--basic;
        }
    }
}

.card--testimonial-feature__details__row {
    display: flex;
    margin-left: $measure--medium4;
    margin-right: $measure--medium4;

    @include media('<desktop') {
        margin-left: $measure--small2;
        margin-right: $measure--small2;
    }
}

.card--testimonial-feature__details__column {
    &:first-child {
        margin-right: $measure--small2;

        @include media('<desktop') {
            margin-right: $measure--basic;
        }
    }
}

.card--testimonial-feature__details__quote {
    @include standfirst();
    margin-top: 0;
    color: $color-gray10;
}

.card--testimonial-feature__details__quote-marks {
    @include zetatype();
    @include font-family('heading');
    color: $color-pink90;
}

.card--testimonial-feature__details__author__details {
    @include smalltype();
    color: $color-gray7;
    margin-top: $measure--small4;

    a {
        @include anchor-inline();
        @include h5style();
    }
}
