////
/// Statistics block
/// @group patterns
////

@mixin statistics-block($color: $color-red50) {
    .statistics-block {
        .content-wrapper {
            @include media('<tablet') {
                margin: 0;
            }
        }
    }

    .statistics-block__heading {
        padding: $measure--basic;
        text-align: center;
    }

    .ksp__items {
        @include standard-grid();
        margin: $measure--basic $measure--small2;
        @include media('<tablet') {
            margin: $measure--basic 0;
        }

        // Generic properties for all statistics
        .ksp__item {
            padding: $measure--basic;
            margin: $measure--half 0;
            &:last-of-type {
                border-right: 0;
            }
            border-right: $border-width-default $border-style-solid $color-gray1;

            // Control vertical right border depending on device width
            @include media('<tablet') {
                border-right: 0;
                padding: 0;
                margin: 0;
            }
            h2 {
                @include h1style();
                font-weight: $font-weight-light;
                .animate--number__unit--post {
                    @include alphatype();
                    margin-left: $measure--quarter;
                }
            }
        }
        .ksp__item__overline {
            @include font-family('default');
            font-weight: $font-weight-book;
            font-size: $font-size--alpha;
            color: $color-gray7;
        }
        .ksp__item__pre-figure,
        .ksp__item__figure,
        .ksp__item__post-figure,
        .ksp__item__text-only {
            @include font-family('heading');
            font-weight: $font-weight-light;
            display: inline;
            color: $color;
        }
        .ksp__item__text {
            @include font-family('default');
            font-size: $font-size--alpha;
            font-weight: $font-weight-text;
            color: $color-gray10;
            margin: $measure--basic 0;
        }
        .ksp__item__icon {
            width: 52px; // Icon height matches text height.
            margin: 0 auto;
            display: block;
            @include media('<tablet') {
                margin: 0;
            }
        }
        .ksp__item__heading--top-padding,
        .ksp__item__icon--top-padding {
            padding-top: 2.1rem; // Fills gap if no overline exists. Exact to match height if overline was present.
            @include media('<tablet') {
                padding-top: $measure--basic;
            }
        }

        // Animation
        .animation-incomplete {
            opacity: 0;
        }
        .animation-incomplete.animation-complete {
            opacity: 1;
            transition: opacity 1500ms;
        }

        // Control individual statistic width based on parent class
        &.ksp__items--2 {
            @include media('<tablet') {
                grid-template-rows: repeat(2, auto);
                grid-template-columns: repeat(6, 1fr);
                grid-template-areas:
                    'a a a a a a'
                    'b b b b b b';
            }
            @include media('>=tablet', '<=desktop') {
                grid-template-columns: repeat(12, 1fr);
                grid-template-areas: '. a a a a a b b b b b .';
            }
            @include media('>desktop') {
                grid-template-columns: repeat(18, 1fr);
                grid-template-areas: '. . a a a a a a a b b b b b b b . .';
            }

            .ksp__item {
                &:nth-child(1) {
                    grid-area: a;
                }
                &:nth-child(2) {
                    grid-area: b;
                }
            }
        }

        &.ksp__items--3 {
            @include media('<tablet') {
                grid-template-rows: repeat(3, auto);
                grid-template-columns: repeat(6, 1fr);
                grid-template-areas:
                    'a a a a a a'
                    'b b b b b b'
                    'c c c c c c';
            }
            @include media('>=tablet', '<=desktop') {
                grid-template-rows: repeat(2, auto);
                grid-template-columns: repeat(12, 1fr);
                grid-template-areas:
                    '. a a a a a b b b b b .'
                    '. . . c c c c c c . . .';

                .ksp__item {
                    &:nth-child(2) {
                        border-right: 0;
                    }
                    &:nth-child(3) {
                        margin: 0 auto;
                    }
                }
            }
            @include media('>desktop') {
                grid-template-columns: repeat(18, 1fr);
                grid-template-areas: '. . . a a a a b b b b c c c c . . .';
            }
            .ksp__item {
                &:nth-child(1) {
                    grid-area: a;
                }
                &:nth-child(2) {
                    grid-area: b;
                }
                &:nth-child(3) {
                    grid-area: c;
                }
            }
        }

        &.ksp__items--4 {
            @include media('<tablet') {
                grid-template-rows: repeat(4, auto);
                grid-template-columns: repeat(6, 1fr);
                grid-template-areas:
                    'a a a a a a'
                    'b b b b b b'
                    'c c c c c c'
                    'd d d d d d';
            }
            @include media('>=tablet', '<=desktop') {
                grid-template-rows: repeat(2, auto);
                grid-template-columns: repeat(12, 1fr);
                grid-template-areas:
                    '. a a a a a b b b b b .'
                    '. c c c c c d d d d d .';

                .ksp__item {
                    &:nth-child(2) {
                        border-right: 0;
                    }
                }
            }
            @include media('>desktop') {
                grid-template-columns: repeat(18, 1fr);
                grid-template-areas: '. a a a a b b b b c c c c d d d d .';
            }
            .ksp__item {
                &:nth-child(1) {
                    grid-area: a;
                }
                &:nth-child(2) {
                    grid-area: b;
                }
                &:nth-child(3) {
                    grid-area: c;
                }
                &:nth-child(4) {
                    grid-area: d;
                }
            }
        }
    }
}

.statistics-block {
    @include statistics-block();
}
