@mixin loadMore {
    .item-anchor {
        display: none;
    }

    .icon-plus-circle {
        margin-right: $measure--quarter;
        border: 0;
        border-bottom: $border-width-default $border-style-solid transparent;
    }

    .load-more-label {
        border-bottom: $border-width-default $border-style-solid #006bbd;
        display: inline-block;
    }

    .load-more-btn {
        @include anchor-inline(
            $color-inline-link,
            $color-inline-link,
            $color-text--dark,
            $color-outline
        );
        border-bottom: $border-width-default $border-style-solid transparent;
        margin: $measure--small3 auto;
        display: block;
        &.hide {
            display: none;
        }
    }

    .items-group {
        margin-left: 0;
        margin-right: 0;
    }

    .item {
        display: grid;
        list-style-type: none;
        &.hide {
            display: none;
        }
    }
}

.load-more {
    @include loadMore();
}
