.card--social-media {
    background: $color-purewhite;
    margin-bottom: $measure--small4;
    padding: $measure--small2;
    @include box-shadow;
}

.card--social-media__header {
    div:first-child {
        margin-bottom: $measure--half;
    }

    @include media('>mobile') {
        display: flex;

        div:first-child {
            margin-right: $measure--basic;
            margin-bottom: 0;
        }
    }

    span {
        margin-right: $measure--quarter;
    }
}

.card--social-media__source-title {
    span.fa-twitter {
        color: $color-blue30;
    }
}

.card--social-media__source__link {
    span {
        color: $color-gray4;
    }
}

.card--social-media__content {
    margin: $measure--small3 0;
}

.card--social-media__controls {
    a {
        color: $color-gray7;
        border-bottom: none;
        transition: none;
        margin-right: $measure--small1;

        &:hover,
        &:active,
        &:focus,
        &:visited {
            color: $color-gray7;
            background-image: none;
            border-bottom: none;
            background: none;
        }
    }
}

.card--social-media__time-info {
    margin-top: $measure--small3;
    border-top: $border-width-default $border-style-solid $color-gray3;
    padding-top: $measure--small1;
    color: $color-gray7;
    font-style: italic;
    padding-top: $measure--basic;
    border-top: $border-style-solid $border-width-default $color-gray3;
}
