.sr-only-focusable-within:active, .sr-only-focusable-within:focus, .sr-only-focusable-within:focus-within {
    clip: auto;
    height: auto;
    margin: 0;
    overflow: visible;
    position: static;
    width: auto;
}

/**
    Used where card has two divs, one most likely
    being a thumbnail.
**/
/*
    USAGE:

    $pseudo: both / before / after
    $background-color: any hex color
    $skew: a degree value

    Background color must match background color of element mixin is applied to
*/
.heading-centered {
    text-align: center;
}

.heading-centered:after {
    content: '';
    width: 2.5rem;
    height: 0.25rem;
    display: block;
    background: #d61726;
    margin: 0.25rem auto 1rem auto;
}

@keyframes slide-left-in {
    from {
        transform: translateX(-100%);
    }
    to {
        transform: none;
    }
}

@keyframes slide-right-out {
    from {
        transform: none;
    }
    to {
        transform: translateX(100%);
    }
}

/**
 *
 * Call to action Primary style on a dark background
 * To use @include cta-primary-dark-bg();
 * this will style the CTA to default primary colours
 *
 * to modify the colours pass in the background colour and colour as parameters
 * e.g @include cta-primary-dark-bg(yellow, green);
 *
 *
 */
/**
 *
 * Call to action style for a light background
 * Primary colors will be used if no parameters is pass as args
 * e.g @include cta-primary-light-bg();
 *
 * to modify the colours used pass in a backgound or colour arg
 * e.g @include cta-primary-light-bg(yellow, green);
 *
 */
/**
 *
 * Call to action outlined on a dark background
 * to use just call @include cta-outlined-dark-bg();
 *
 */
/**

     TODO:
     - Allow mixin to accept parameters to change default colours?

  */
/**

    TODO:
    - Allow mixin to accept parameter to change default colours

 */
/**
 *
 * CTA on a light background
 * to use with default colours
 * e.g @include cta-basic-light-bg();
 *
 * colours can be modify by passing arg to the mixin
 * e.g @include cta-basic-light-bg(yellow, green);
 * this will change the background to yellow and color to green
 *
 */
/**
 *
 * CTA on a dark background
 * To used the default colour
 * e.g @include cta-basic-dark-bg();
 *
 * to modify the colour used you can pass in arg to the mixin
 * e.g @include cta-basic-dark-bg(yellow, blue);
 * this will modify the background and color attributes
 *
 */
html {
    text-size-adjust: 100%;
}

body {
    margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
figure,
ol,
ul,
dl,
dt,
dd,
fieldset {
    border: 0;
    margin: 0;
    padding: 0;
}

main {
    display: block;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-size: inherit;
}

b,
strong {
    font-weight: 700;
}

a,
button {
    color: inherit;
}

a {
    text-decoration: none;
}

button {
    background: none;
    border: 0;
    cursor: pointer;
    font: inherit;
    letter-spacing: inherit;
    overflow: visible;
    padding: 0;
    touch-action: manipulation;
}

::-moz-focus-inner {
    padding: 0;
    border: 0;
}

img {
    max-width: 100%;
    height: auto;
    border: 0;
}

iframe {
    display: block;
    margin: 1rem;
}

