    .js main > .navigation--secondary + .course--ug .tabs__panel:not([hidden]) > .tabs__panel__content .tabs__panel__body {
        grid-column: 4 / span 12;
    }
}

@media screen {
    .js main > .navigation--secondary + .course--ug .tabs .tabs {
        margin: 0;
        padding: 0;
    }
    .js main > .navigation--secondary + .course--ug .tabs .tabs .tabs__panel:not([hidden]) {
        padding: 1.25rem 0;
    }
    .js main > .navigation--secondary + .course--ug .tabs .tabs .tabs__panel__content {
        display: block;
        padding: 0 1rem;
    }
    .js main > .navigation--secondary + .course--ug .tabs .tabs .tabs__panel__body {
        margin-top: 1.25rem;
    }
}

.course--ug__tabs__panel__inner--during {
    grid-column: 2/-1;
}

@media (max-width: 900px) {
    .course--ug__student-testimonials__half--unibuddy {
        margin-bottom: 2rem;
    }
}

.course--ug__student-testimonials__half--unibuddy .unibuddy-card {
    margin-top: 1rem;
}
