.footer-cards {
    padding: $measure--small4 $measure--small4;

    @include media('>between') {
        padding: $measure--small4 $measure--medium11;
    }

    h2 {
        padding-bottom: $measure--basic;
    }

    h2:nth-of-type(2n) {
        margin-top: $measure--medium7;
    }
}

.footer-cards__contact {
    margin-left: 0;

    li {
        list-style-type: none;

        @include media('<tablet') {
            &:nth-of-type(2n) {
                .footer-cards__contact__name {
                    margin-top: $measure--small4;
                }
            }
        }
    }

    .icon {
        margin-right: $measure--basic;
    }

    @include media('>=tablet') {
        display: flex;
        justify-content: space-between;
    }
}

.footer-cards__contact__name {
    margin-bottom: $measure--quarter;

    @include media('>=tablet') {
        margin-bottom: $measure--small4;
    }
}

.footer-cards--pink80 {
    color: $color-purewhite;
    background-color: $color-pink80;

    a {
        @include anchor-inline(
            $color-gray4,
            $color-gray4,
            $color-gray11,
            $color-outline--dark
        );
    }

    h2 {
        @include full-width-heading($color-pink70);
    }
}
