/* -- General event card styles applied to all -- */

.card--event {
    background: $color-purewhite;

    &:hover {
        @include media('hover') {
            .thumbnail__image-container {
                img {
                    transform: scale(1.04);
                }

                &:after {
                    background-color: rgba(0, 0, 0, 0.1);
                }
            }
        }
    }

    .underline-transition__title {
        @include underline-transition();
        @include h5style;
    }
}

.card--event__icon {
    color: $color-red50;
    width: $measure--basic;
    font-size: $font-size--basic;
    margin-right: $measure--half;
}

.card--event__image-container {
    @include thumbnail-gradient-scale;

    .thumbnail__image-container {
        height: $measure--huge5;
        width: 100%;

        @include media('>tablet') {
            height: 16.66rem;
            width: 100%;
        }
    }
}

.card--event__date--highlight,
.card--event__date {
    @include smalltype;
    color: $color-purewhite;
    background: $color-red50;
    padding: $measure--half $measure--threequarters $measure--half
        $measure--threequarters;
    font-weight: $font-weight-bold;

    span {
        font-weight: $font-weight-light;
    }
}

.card--event__content {
    padding: 0 $measure--small3 $measure--small3 $measure--small3;
    position: relative;
}

.card--event__date-time-text {
    @include font-family('default');
    @include smalltype;
    color: $color-gray10;
    margin-right: $measure--basic;

    p {
        @include smalltype;
    }
}

.card--event__category {
    padding: $measure--half $measure--threequarters;
    margin: 0;
}

.card--event__location {
    margin-bottom: $measure--basic;
    @include smalltype;
}

.card--event__type {
    position: relative;
    @include smalltype;

    span {
        color: $color-red50;
    }
}

.card--event__price {
    margin-bottom: $measure--basic;
    @include smalltype;

    span {
        border: $border-width-thick $border-style-solid $color-red50;
        padding: $measure--half;

        @include media('>tablet') {
            padding: $measure--threequarters $measure--basic;
        }
    }
}

/* -- Portrait/landscape specific styles -- */

.card--event--portrait {
    @include box-shadow;

    .card--event__date--highlight {
        display: inline-flex;
        position: relative;
        top: -$measure--small1;
        left: $measure--small3;
        align-items: center;
    }

    .card--event__price {
        text-align: right;
        margin-top: $measure--threequarters;

        span {
            display: inline-block;
        }
    }

    .card--event__date-time-text {
        margin: $measure--basic 0;
    }
}

.card--event--landscape {
    padding: $measure--basic;
    @include background-color-and-scale;
    background: transparent;
    position: relative;

    @include media('>tablet') {
        display: flex;
    }

    .thumbnail__image-container {
        height: 100%;
    }

    .card--event__image-container {
        position: relative;

        @include media('>tablet') {
            width: 22.22rem;
        }
    }

    .card--event__date--highlight {
        position: absolute;
        right: 0;
        top: $measure--small4;
    }

    .card--event__date-time-text {
        margin-bottom: $measure--basic;
    }

    .card--event__content {
        margin-top: $measure--small4;
    }

    .card--event__price {
        position: absolute;
        right: $measure--small2;
        bottom: $measure--medium1;

        @include media('>tablet') {
            top: $measure--half;
            bottom: initial;
        }
    }

    .card--event__inner {
        @include media('>tablet') {
            display: flex;
            margin-top: $measure--medium2;
        }

        div:nth-child(2) {
            margin-top: $measure--basic;

            @include media('>tablet') {
                margin-top: 0;
            }
        }
    }

    .card--event__date {
        margin-top: $measure--small4;
    }
}

.card--event--transparent {
    box-shadow: none;
    background: transparent;
}

.card--event__category {
    color: $color-gray7;
    font-style: italic;
    padding: $measure--half $measure--basic;
    display: inline-block;
}
