////
/// Content
/// @group base
////
html {
    @include font-family('default');
    @include body-font-features();

    font-size: $font-size--mobile;
    font-style: normal;
    font-weight: $font-weight-text;
    line-height: 1;
    text-rendering: optimizeLegibility;

    @include media('>=desktop', '<1080p') {
        font-size: $font-size--desktop;
    }

    @include media('>=1080p', '<4K') {
        font-size: $font-size--1080p;
    }

    @include media('>=4K', '<8K') {
        font-size: $font-size--4K;
    }

    @include media('>=8K') {
        font-size: $font-size--8K;
    }
}

article {
    a {
        @include anchor-inline();
    }
}

u {
    text-decoration: none;
}

.fa,
.fab,
.fad,
.fal,
.far,
.fas {
    font-size: $font-size--icon;
}

.fal {
    @include font-family('icon');
    font-weight: $font-weight-light;
}

.far {
    @include font-family('icon');
    font-weight: $font-weight-normal;
}

.fa,
.fas {
    @include font-family('icon');
    font-weight: $font-weight-extrablack;
}

.fab {
    @include font-family('brands');
    font-weight: $font-weight-normal;
}

.fad {
    @include font-family('duotone');
    font-weight: $font-weight-extrablack;
}

.sans {
    @include font-family('default');
    font-weight: $font-weight-text;

    b,
    strong {
        font-weight: $font-weight-bold;
    }

    i,
    em {
        font-style: italic;
    }
}

.serif {
    @include font-family('serif');
    font-weight: $font-weight-text;

    b,
    strong {
        font-weight: $font-weight-bold;
    }

    i,
    em {
        font-style: italic;
    }
}

.mono {
    @include font-family('mono');
    font-weight: $font-weight-text;

    b,
    strong {
        font-weight: $font-weight-bold;
    }

    i,
    em {
        font-style: italic;
    }
}

.meta {
    @include font-family('heading');
    @include heading-font-features();

    font-weight: $font-weight-normal;

    b,
    strong {
        font-weight: $font-weight-bold;
    }

    i,
    em {
        font-style: italic;
    }
}

.weight-hairline {
    font-weight: $font-weight-hairline;
}

.weight-thin {
    font-weight: $font-weight-thin;
}

.weight-light {
    font-weight: $font-weight-light;
}

.weight-normal {
    font-weight: $font-weight-normal;
}

.weight-text {
    font-weight: $font-weight-text;
}

.weight-book {
    font-weight: $font-weight-book;
}

.weight-medium {
    font-weight: $font-weight-medium;
}

.weight-bold {
    font-weight: $font-weight-bold;
}

.weight-black {
    font-weight: $font-weight-black;
}

.weight-extrablack {
    font-weight: $font-weight-extrablack;
}

.style-fraction {
    @include body-fraction-font-features();
}

@mixin body-baseline() {
    @include font-family('default');
    @include body-font-features();
    @include alphatype();
    font-style: normal;
    font-weight: $font-weight-text;

    b,
    strong {
        font-weight: $font-weight-bold;
    }

    i,
    em {
        font-style: italic;
    }
}

@mixin content-style() {
    @include body-baseline();
    max-width: $max-line-length;
}

.content-style {
    @include content-style();
}

@mixin mono-style() {
    @include content-style();
    @include font-family('mono');
}

code,
pre {
    @include mono-style();
    letter-spacing: 0;
    max-width: unset;
}

p code {
    background: $color-gray9;
    border-radius: $measure--quarter;
    color: $color-purewhite;
    padding-left: $measure--quarter;
    padding-right: $measure--quarter;
}

@mixin heading-style() {
    @include font-family('heading');
    @include heading-font-features();

    b,
    strong {
        font-weight: inherit;
    }

    i,
    em {
        font-style: italic;
    }

    u {
        text-decoration: none;
    }
}

@mixin h6style() {
    @include heading-style();
    @include alphatype();
    font-style: normal;
    font-weight: $font-weight-medium;
    line-height: $line-height-ratio--heading;
    margin-top: $heading-margin;

    b,
    strong {
        font-weight: $font-weight-medium;
    }
}

h6,
.h6-style {
    @include h6style();
}

@mixin h5style() {
    @include heading-style();
    @include betatype();
    font-style: normal;
    font-weight: $font-weight-medium;
    margin-top: $heading-margin;

    b,
    strong {
        font-weight: $font-weight-medium;
    }
}

h5,
.h5-style {
    @include h5style();
}

@mixin h4style() {
    @include heading-style();
    @include gammatype();
    font-style: normal;
    font-weight: $font-weight-medium;
    margin-top: $heading-margin;

    b,
    strong {
        font-weight: $font-weight-medium;
    }
}

h4,
.h4-style {
    @include h4style();
}

@mixin h3style() {
    @include heading-style();
    @include deltatype();
    font-style: normal;
    font-weight: $font-weight-medium;
    margin-top: $heading-margin;

    b,
    strong {
        font-weight: $font-weight-medium;
    }
}

h3,
.h3-style {
    @include h3style();
}

@mixin h2style() {
    @include heading-style();
    @include epsilontype();
    font-style: normal;
    font-weight: $font-weight-medium;
    margin-top: $heading-margin;

    b,
    strong {
        font-weight: $font-weight-medium;
    }
}

h2,
.h2-style {
    @include h2style();
}

@mixin h1style() {
    @include heading-style();
    @include zetatype();

    @include media('>=tablet', '<=between') {
        @include etatype();
    }

    @include media('>between') {
        @include thetatype();
    }

    font-style: normal;
    font-weight: $font-weight-medium;

    b,
    strong {
        font-weight: $font-weight-medium;
    }
}

@mixin heading--centered($color) {
    text-align: center;

    &:after {
        background-color: $color;
        content: '';
        display: block;
        height: $measure--quarter;
        margin-left: auto;
        margin-right: auto;
        margin-top: $measure--half;
        width: $measure--large2;
    }
}

h1,
.h1-style {
    @include h1style();
}

h1,
h2,
h3,
h4,
h5,
h6 {
    & + h2,
    & + h3,
    & + h4,
    & + h5,
    & + h6 {
        margin-top: $heading-margin;
    }
}

@mixin standfirst() {
    @include gammatype();
    @include font-family('heading');
    @include heading-font-features();

    font-style: normal;
    font-weight: $font-weight-light;
    line-height: $line-height-ratio--body;
    margin-top: $content-element-margin;
    max-width: $max-line-length;
}

main {
    position: relative; // To stop full-page background fill on Finder pattern
    p,
    ol,
    ul,
    dl,
    blockquote {
        @include content-style();
    }

    p,
    ol,
    ul,
    dl,
    blockquote,
    figure {
        margin-top: $content-element-margin;
    }

    sup,
    sub {
        font-size: $font-size--super;
        line-height: $font-size--super;
    }

    header {
        p {
            @include h2style();
            font-weight: $font-weight-light;
            margin-top: 0;
        }
    }

    .standfirst,
    .standfirst-style {
        @include standfirst();
    }

    p > img {
        margin-top: $content-element-margin;
    }

    ol {
        margin-left: $measure--medium1;
        max-width: calc(#{$max-line-length} - #{$measure--medium1});
    }

    ul {
        margin-left: $measure--small1;
        max-width: calc(#{$max-line-length} - #{$measure--small1});
    }

    dl {
        max-width: $max-line-length;
    }

    ul {
        list-style: disc;

        ul {
            list-style: circle;
        }
    }

    dt {
        @include h6style();

        &:first-of-type {
            margin-top: 0;
        }
    }

    li {
        margin-top: $content-element-margin;
    }

    table {
        display: block;
        margin-top: $content-element-margin;
        min-width: 66%;
        max-width: 100%;
        overflow-x: scroll;

        th {
            @include h6style();
            vertical-align: top;
        }

        td {
            @include content-style();
            vertical-align: top;
        }
    }
}
