.feedback {
    @include media('print') {
        display: none;
    }

    @include standard-box();
    @include alphatype();

    background-color: $color-gray13;
    display: block;
    margin-bottom: $measure--small2;
    margin-top: $measure--small2;
    padding-bottom: $measure--half;
    padding-left: $measure--small2;
    padding-right: $measure--small2;
    padding-top: $measure--half;

    @include media('>=between') {
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        justify-content: space-between;
    }

    .feedback__form-link,
    .feedback__direct {
        @include content-style();

        margin-top: 0;
        max-width: unset;
    }

    .feedback__direct {
        legend {
            float: left; // unavoidable float because legend sucks
            margin-bottom: $measure--basic;
            margin-right: $measure--half;
            margin-top: $measure--basic;
        }

        p {
            margin-bottom: $measure--basic;
            margin-top: $measure--basic;
        }

        .feedback__direct__buttons {
            display: inline-flex;
            flex-direction: row;
            flex-wrap: nowrap;
        }

        button {
            @include cta-basic-light-bg($color-purewhite, $color-nearblack);

            box-shadow: none;
            margin-bottom: $measure--half;
            margin-top: $measure--half;

            @include media('<=mobile') {
                display: inline-block;
            }

            & + button {
                margin-left: $measure--basic;
            }
        }
    }

    .feedback__form-link {
        margin-bottom: $measure--basic;
        margin-top: $measure--basic;

        a {
            @include anchor-inline();
        }
    }
}
