.imageColLayout-wrap__section {
    @include standard-grid();
    @include standard-box;

    h2 {
        grid-column: 1/-1;
    }
}

.imageColLayout {
    column-count: 4;
    column-gap: $measure--basic;
    grid-column: 1/-1;
    margin: $measure--basic 0;

    @include media('>mobile', '<=between') {
        column-count: 2;
    }

    @include media('<=mobile') {
        column-count: 1;
    }
}

.imageColLayout--card {
    display: inline-block;
    width: 100%;
}

.imageColLayout-card--image {
    img {
        min-width: 100%;
    }
}
