// <Desktop-specific layout - secondary navigation never present
main > .case-study {
    @include media('<small') {
        .banner-standfirst-label {
            .banner__image {
                grid-column: 1 / span 24;
            }
        }

        .banner__standfirst {
            @include default-margin();
        }
    }
}

// Desktop-specific layout
main {
    @include media('>=small') {
        .banner-standfirst-label {
            .banner__image {
                grid-column: 1 / span 24;
            }
        }
    }
}

// Desktop specific layout if secondary navigation is present
main {
    @include media('>=small') {
        .navigation--secondary {
            & + article {
                .banner-standfirst-label {
                    .banner__image {
                        grid-column: 1 / span 18;
                    }
                }
            }
        }
    }
}

// Generic styles
.banner-standfirst-label {
    .banner__image {
        overflow: hidden;
    }

    .banner__block-color {
        background-color: $color-gray2;
        grid-column: 1 / -1;
        height: $measure--huge1;

        @include media('<tablet') {
            height: $measure--large1;
        }
    }

    .banner__standfirst {
        background: $color-purewhite;
        bottom: 0;
        color: $color-nearblack;
        grid-column: 1 / -1;
        opacity: 1;
        text-align: center;

        @include media('>tablet') {
            bottom: $measure--medium9;
            grid-column: 2 / 24;
        }

        @include media('>small') {
            bottom: $measure--medium9;
            grid-column: 2 / 18;
        }

        @include media('>desktop') {
            grid-column: 3 / 17;
        }
    }

    .labels {
        @include smalltype();

        color: $color-gray7;
        font-weight: $font-weight-bold;
        margin-bottom: $measure--small2;
    }

    .standfirst {
        margin-left: auto;
        margin-right: auto;
        text-align: center;
    }
}
