// <Desktop-specific layout - secondary navigation never present
main > .case-study {
    @include media('<small') {
        .wrapper__case-study__overview__main {
            @include default-margin();

            .case-study__overview__main,
            .case-study__overview__multimedia-banner {
                @include default-padding();
            }
        }

        .case-study--research__details__section,
        .case-study__knowledge,
        .case-study__contact-details__card {
            @include standard-box();
        }

        .case-study--research__details {
            h2 {
                @include standard-box();
            }
        }
    }

    @include media('<=mobile') {
        .case-study__content__container {
            @include standard-box();
        }
    }

    @include media('>mobile', '<small') {
        .case-study__content__container {
            @include x-column-grid(24);

            display: grid;

            .case-study__content,
            .case-study--research__details,
            .case-study__ktp-details__container__content {
                grid-column: 7 / span 12;
            }
        }
    }
}

// Desktop-specific layout
main {
    @include media('>=small') {
        @include standard-box();
        @include x-column-grid(24);

        > .case-study {
            grid-column: 1 / span 24;

            .wrapper__case-study__overview__main,
            .case-study__overview__multimedia-banner,
            .case-study__content__container,
            .case-study--research__details__wrap__container,
            .case-study__ktp-details__container {
                @include x-column-grid(24);

                display: grid;

                .case-study__overview__main {
                    @include x-column-grid(22);

                    display: grid;
                    grid-column: 2 / -2;

                    .case-study__overview__main__details {
                        grid-column: 3 / -3;
                    }
                }

                .case-study__overview__multimedia-banner__video,
                .case-study__overview__multimedia-banner__image {
                    grid-column: 6 / -6;

                    .embedded-video {
                        margin-top: $measure--small2;

                        padding-bottom: 0;
                        padding-left: 0;
                        padding-right: 0;
                        padding-top: 0;
                    }

                    .youtube-video {
                        width: 100%;
                    }
                }

                .case-study__content,
                .case-study--research__details,
                .case-study__ktp-details__container__content {
                    grid-column: 7 / span 12;
                }
            }
        }
    }
}

// Desktop specific layout if secondary navigation is present
main {
    @include media('>=small') {
        .navigation--secondary {
            grid-column: 1 / span 6;

            & + .case-study {
                grid-column: 7 / span 18;

                .wrapper__case-study__overview__main,
                .case-study__overview__multimedia-banner,
                .case-study__content__container,
                .case-study--research__details__wrap__container,
                .case-study__ktp-details__container {
                    @include content-grid();

                    .case-study__overview__main {
                        @include x-column-grid(16);

                        display: grid;
                        grid-column: 2 / -2;

                        .case-study__overview__main__details {
                            grid-column: 3 / -3;
                        }
                    }

                    .case-study__overview__multimedia-banner__video,
                    .case-study__overview__multimedia-banner__image {
                        grid-column: 3 / -3;

                        .embedded-video {
                            margin-top: $measure--small2;

                            padding-bottom: 0;
                            padding-left: 0;
                            padding-right: 0;
                            padding-top: 0;
                        }

                        .youtube-video {
                            width: 100%;
                        }
                    }

                    .case-study__content,
                    .case-study--research__details,
                    .case-study__ktp-details__container__content {
                        grid-column: 4 / span 12;
                    }
                }
            }
        }
    }
}

// Generic styles
.case-study__overview__main {
    background-color: $color-gray13;
    padding-bottom: $measure--basic;
    padding-top: $measure--basic;
}

.case-study--research {
    .case-study__overview__main {
        padding: $measure--small2 0 $measure--medium1 0;
    }
}

.case-study--business {
    .case-study__overview__main {
        @include media('>between') {
            padding-top: $measure--small4;
        }
    }

    .case-study__knowledge {
        .cta {
            margin: $measure--small1 0 $measure--medium3 0;
        }
    }
}

.case-study__content {
    margin-bottom: $measure--basic;

    h2 {
        margin-top: $measure--small2;
    }
}

.case-study__overview__main__details__company-cards {
    @include media('>=tablet') {
        display: flex;
        margin-bottom: $measure--basic;
    }
}

.case-study__company-card {
    background-color: $color-purewhite;
    border-bottom: $border-width-hairline $border-style-solid $color-gray3;
    border: $border-width-hairline $border-style-solid $color-gray3;
    color: $color-gray7;
    display: flex;
    margin-bottom: $measure--basic;
    max-height: $measure--large3;
    min-height: $measure--large3;
    transition: none;
    width: 100%;

    @include media('>tablet') {
        margin-bottom: 0;
        width: 20rem;
    }

    &:first-child {
        margin-right: $measure--basic;
    }

    &:focus,
    &:visited {
        color: $color-nearblack;
        background-image: none;
        background-color: $color-purewhite;
        border-bottom: $border-width-hairline $border-style-solid $color-gray3;
        @include box-shadow;

        .company-card__name {
            &:after {
                border-bottom-color: $color-nearblack;
                border-bottom-style: $border-style-solid;
                border-bottom-width: $border-width-thick;
            }
        }
    }

    &:hover {
        @include media('hover') {
            color: $color-nearblack;
            background-image: none;
            background-color: $color-purewhite;
            border-bottom: $border-width-hairline $border-style-solid
                $color-gray3;
            @include box-shadow;

            .company-card__name {
                &:after {
                    border-bottom-color: $color-nearblack;
                    border-bottom-style: $border-style-solid;
                    border-bottom-width: $border-width-thick;
                }
            }
        }
    }

    figure {
        margin-top: 0;
        overflow: hidden;
    }

    .figure--image {
        align-items: center;
        display: flex;
        justify-content: center;
        padding: $measure--basic;
        position: relative;
        width: $measure--medium11;

        picture {
            height: 100%;
            text-align: center;

            img {
                height: auto;
                max-height: 100%;
                max-width: 100%;
                width: auto;
            }
        }

        @include media('>tablet') {
            width: $measure--large4;
        }

        &:after {
            background-color: $color-gray2;
            content: '';
            height: $measure--medium9;
            position: absolute;
            right: 0;
            top: $measure--basic;
            width: 1px;
        }
    }
}

.company-card__name {
    padding-bottom: $measure--quarter;
    position: relative;

    &:after {
        bottom: 0;
        content: '';
        height: $border-width-thick;
        left: 0;
        position: absolute;
        width: 100%;
    }

    .fa-external-link {
        margin-left: 0;
    }
}

.company-card__name-title {
    @include font-family('heading');

    margin-bottom: $measure--half;
}

.company-card__funder {
    margin-bottom: $measure--quarter;
}

.case-study__company-card__details {
    align-items: center;
    display: flex;
    padding-left: $measure--basic;
}

.case-study__overview__main__details {
    .label {
        font-weight: $font-weight-medium;
        margin-right: $measure--half;
    }

    .case-study__overview__main__details__result {
        display: flex;
        flex-direction: row;
    }
}

.case-study__content__share-icons__wrap {
    margin-bottom: $measure--small4;
    margin-left: 0;
    margin-right: 0;
    margin-top: $measure--small4;
}

.case-study--research__details {
    padding-bottom: $measure--small2;
}

.case-study__content__share-icons {
    border-bottom: $border-width-hairline $border-style-solid $color-gray2;
    border-top: $border-width-hairline $border-style-solid $color-gray2;
    position: relative;
}

.case-study__content__share-icons__icons {
    ul {
        list-style-type: none;
        margin: $measure--small2 0;
        display: flex;
        max-width: none;
    }

    li {
        margin-right: $measure--threequarters;
        margin-top: 0;
    }

    li.copyLink {
        margin-right: 0;
    }
}

.wrapper--case-study__ktp-details {
    background-color: $color-gray1;
    margin-top: $measure--small1;

    .case-study__ktp-details__container__content {
        padding-bottom: $measure--medium4;

        .icon {
            color: $color-red50;
        }
    }
}

.case-study__related-case-studies__wrap {
    h2,
    h3 {
        text-align: center;
    }

    .related-content {
        @include standard-box();
    }
}

.case-study__related-case-studies {
    .related-content__heading {
        margin-top: $measure--small4;
    }
}

.case-study--research__details__wrap {
    background-color: $color-gray1;
}

.case-study--research__details__section {
    background-color: $color-purewhite;
    margin-top: $measure--basic;
    padding: $measure--basic;

    .icon {
        margin-right: $measure--half;
    }

    .label {
        color: $color-gray7;
        font-weight: $font-weight-medium;
    }
}

.case-study__contact-details__card {
    .related-content {
        @include media('>=tablet') {
            display: flex;
        }

        .card {
            &:nth-child(2) {
                margin-top: $measure--basic;

                @include media('>=tablet') {
                    margin-top: 0;
                }
            }

            @include media('>=tablet') {
                width: 50%;
                margin-right: $measure--small2;
            }
        }
    }
}
